
export function memoizeWithWeakMaps2<T extends object, U extends object, V>(g: (arg1: T, arg2: U) => V): (arg1: T, arg2: U) => V {
    const cache = new WeakMap<T, WeakMap<U, V>>();

    return (arg1: T, arg2: U): V => {
        if (!cache.has(arg1)) {
            cache.set(arg1, new WeakMap<U, V>());
        }

        const innerCache = cache.get(arg1)!;
        if (!innerCache.has(arg2)) {
            innerCache.set(arg2, g(arg1, arg2));
        }

        return innerCache.get(arg2)!;
    };
}