import {Bounds} from "../Bounds";
import {Angle, Point} from "./Point";
import {Ch, Path, Piece, singularity} from "./Path";
import {NPoint} from "../Reacts/Mos";
import {anglePreservingTransformation, PRECISION_EPSILON, shapeColor} from "./const";
import {DISTORTION_ORTHORGONAL, DISTORTION_STRETCHY, DistortionOption} from "./DragOptions";
import {Segment} from "./Segment";


export class Shape {
    public segs:Path[];
    private _id:string;
    private _svg:string;
    private _area:number;
    constructor(segs: Path[]) {
        this.segs = segs;
    }
    public id() {
        if (!this._id) {
            this._id=this.segs.map(path=>path.id()).join("*");
        }
        return this._id;
    }
    public fullSvgPath() {
        if (!this._svg) {
            let a = this.segs.map(s => s.svgPath()).join("\n");
            this._svg = `M${this.segs[0].start().svg()} ${a}`
        }
        return this._svg;
    }
    public toChain():Path {
        return Chain.of(this.segs.map((seg)=>({path:seg,t1:0,t2:1})));
    }
    public estimatedArea() {
        if (!this._area) {
            let pieces: Piece[] = this.segs.flatMap(path => {
                let frags= path.fragmentize().unchainRecursive();
                return frags;
            });
            let points: Point[] = pieces
                .map(piece=>piece.path.end());
            // y.unshift(x[0].path.start());
            if (points.length<3) return 0;

            // Gauss's formula
            let area = 0;
            let j = points.length - 1;
            for (let i = 0; i < points.length; i++) {
                area += (points[j].x + points[i].x) * (points[j].y - points[i].y);
                j = i;
            }
            this._area = area/2.0;
        }
        return this._area;
    }
}

export function shapeColorFunctionByMedianArea(shapes: Shape[]): (_:Shape)=>string {
    if (!shapes||!shapes.length) return (x)=>"";
    let ss = shapes.map(s=>s.estimatedArea()).sort((a,b)=>a-b);
    let half = Math.floor(ss.length/2);
    let medianSize = ss.length % 2 ? ss[half] : (ss[half-1]+ss[half])/2.0;
    let averageSize: number = shapes.reduce((sum, shape) => sum + shape.estimatedArea(), 0) / shapes.length;
    let medium=averageSize;
    return (shape)=> {
        let colorParam = shape.estimatedArea() / medium;
        return shapeColor(colorParam);
    }
}


/**
 * A compound path made of an unbroken chain.
 * We don't enforce that it doesn't intersect itself ... todo see whether we should!
 */
export class Chain extends Path implements Ch {
    /**
     * Pull the unbroken chains from the input as Chains;
     * Todo, see if we can improve parameterization by using lengths
     */
    static chains(input: Path[]): Path[] {
        let result: Path[] = [];
        while (input.length) {
            let current: Path[] = [input.shift()];
            let currentPoint = current[0].end();
            while (input.length && currentPoint.equals(input[0].start())) {
                currentPoint = input[0].end();
                current.push(input.shift());
            }
            let len=current.length;
            let segs:Piece[]=[];
            for (let i=0;i<current.length;i++) {
                segs.push({path:current[i], t1:i/len,t2:(i+1)/len})
            }
            // todo, don't make chains of single elements, dummy
            result.push(Chain.of(segs));
        }
        return result;
    }
    
    segs: Piece[];
    private _svg: string;
    private _hString: string;
    
    /** Return a simple path or a non-nested Chain.*/
    static of(segs: Piece[]):Path {
        return segs.length === 1
               ? segs[0].path 
               : Chain._of(segs);
    }
    
    // The result will be a chain, even if only one piece.
    static _of(segs: Piece[]):Chain {
        if (!segs.length) 
            throw new Error("Empty segment list");
        
        let totalParamLength = segs.map(cs=>cs.t2-cs.t1).reduce((a,b)=>a+b);
        let t=0;
        segs=segs.slice();
        for (let i=0;i<segs.length;i++) {
            let t1 = t;
            let t2 = t + (segs[i].t2 - segs[i].t1) / totalParamLength;
            segs[i] = {path:segs[i].path, t1:t1, t2:t2};
            t = t2;
        }
        // Some of them might be chains...
        segs = segs.flatMap(cs=>{
            let subchain: Piece[] = cs.path.unchainRecursive();
            // Now subchain is assumed to have good parameter values going from 0 to 1
            // and those need to be mapped to the range cs.t1,cs.t2
            let f = (u:number) => cs.t1 + u * (cs.t2-cs.t1);
            return subchain.map(scs => ({path:scs.path, t1:f(scs.t1), t2:f(scs.t2)}));
        });
        
        return new Chain(segs);
    }
    
    // this works because we enforce they aren't nested
    unchainRecursive(): Piece[] {
        return [...this.segs];
    }

    /**
     * String together the segments.  Note this can handle some segments being reversed.
     */
    protected constructor(segs: Piece[]) {
        super();
        if (segs.length === 0) throw new Error("tried to make empty chain")
        this.segs = segs;
    }

    svgPath(): string {
        if (!this._svg) this._svg=this.segs.map(s => s.path.svgPath()).join("\n");
        return this._svg; 
    }
    start(): Point { 
        return this.segs[0].path.start(); 
    }
    end(): Point { 
        return this.segs[this.segs.length - 1].path.end(); 
    }
    idPrefix():string {return "Ch"}
    
    hString(): string {
        if (!this._hString)  this._hString = `${this.id()}(${(this.segs.map(s => s.path.hString()).join(""))})`;
        return this._hString;
    }

    reverse():Chain {
        return super.reverse() as Chain;
    }
    _reverse = (): Chain => {
        return  Chain._of(this.segs.map(({path, t1, t2}) =>
                                            ({path: path.reverse(), t1:1-t2, t2: 1-t1})).reverse());
    }

    _bounds(): Bounds {
        return  this.segs.map(({path}) => (path.bounds())).reduce((b1, b2) => b1.union(b2));
    }

    isSingularity():singularity|false {
        let isPoint =  this.segs.every(seg=>seg.path.isSingularity());
        if (isPoint) console.log("Chain is found to be a singularity");
        return isPoint ? singularity : false;
    }


    applyLinearTransform(f: (_: Point) => Point): Chain|singularity {
        // if (f(this.start()).equalsfp(f(this.end()))) return singularity;
        
        // This is tricky because we don't want any singularities, 
        // but we might get them if the function maps two non-approximately-equal points
        // to two approximately-equal points. In that case, if we delete the singularity
        // we might end up with gaps in the chain. We'll replace the gaps with
        // segments unless the gaps are actually zero.
        let newSegs:Piece[]=[];
        for (let i=0;i<this.segs.length;i++) {
            let {path,t1,t2}=this.segs[i];
            let path2 = path.applyLinearTransform(f);
            if (path2 === singularity) {
                let a = f(path.start());
                let b = f(path.end());
                if (a.equals(b)) { // strict
                    // skip the singularity
                } else {
                    // replace the singularity with a segment, which really 
                    // isn't ideal for our goal of not having points too close together--- todo xx
                    console.log('applyLinearTransform: including near singularity')
                    newSegs.push({path:Segment.ofNonsingular(a,b),t1,t2})
                }
            } else {
                newSegs.push({path:path2,t1,t2})
            }
        }
        if (newSegs.length===0) return singularity;
        let c = Chain._of(newSegs);
        if (c.bounds().w < PRECISION_EPSILON && c.bounds().h<PRECISION_EPSILON) return singularity;
        return c;
    }
    moveRigidToStart(newStart:Point, newStartTangent:Angle):Chain|singularity {
        // super impl guarantees it's a chain
        return super.moveRigidToStart(newStart,newStartTangent) as Chain|singularity;
    }
    moveRigidToEnd(newEnd:Point, newEndTangent:Angle):Chain|singularity {
        // super impl guarantees it's a chain
        return super.moveRigidToEnd(newEnd,newEndTangent) as Chain|singularity;
    }

    _bend(newStart: NPoint, newStartTangent: Angle, newEndTangent: Angle, newEnd: NPoint, stretch: DistortionOption = DISTORTION_STRETCHY): Path|singularity
    {
        // return super.bend(newStart, newStartTangent,newEndTangent,newEnd);
        // We need to get proper lengths of the subpaths. Redo the chaining with proper parameterization
        let linear:Chain = Chain._of(this.segs.map((seg)=>({path:seg.path, t1:0, t2:seg.path.length()})));
        
        if (stretch===DISTORTION_ORTHORGONAL) {
            let linDirection = linear.end().minus(linear.start());
            let linNewEnd = linear.start().plus(linDirection.toLength(newEnd.dist(newStart)));
            let lin2 = linear.applyLinearTransform(anglePreservingTransformation(
                linear.start(), linear.start(), linear.end(), linNewEnd
            ));
            if (lin2 === singularity) return singularity;
            linear = lin2;
        }  else {
        }

        // The chain is parameterized on t in [0,1] but its length is LEN, which will impact the
        // derivative with respect to t.
        let LEN = linear.segs.map((seg)=>seg.path.length()).reduce((a,b)=>a+b);
        
        let A1 = linear.moveRigidToStart(newStart,newStartTangent) ;
        let B1 = linear.moveRigidToEnd(newEnd,newEndTangent) ;
        if (A1===singularity||B1===singularity) {
            console.log("unexpected singularity")
            return singularity;
        }
        let A=A1 as Chain;
        let B=B1 as Chain;
        
        // A simpler form of Path.spline() which uses the assumption that
        // start and end curves are really translations of the same Chain,
        // and doesn't subdivide but rather recursievely calls bend() on the subpaths
        // Start by applying the original bend algorithm on the polysegments joining the inner points.
        let ts=[0].concat(linear.segs.map((s)=>s.t2));
        // let v:Array<{p1:Point,a1:Angle,a2:Angle,p2:Point}>=new Array();
        let v:{x:Point, xp1?:Angle, xp2?:Angle}[] = [];
        for (let i=0;i<ts.length;i++) {
            let t = ts[i];
            let a = A.pointAt(t);
            let b = B.pointAt(t);
            /*
            interpolates 0 to 1 with zero slope at 0,1;  y=3x^2-2x^3
            i.e. x(t) = (1-u(t))a(t) + u(t)b(t), where  u = 3t^2-2t^3 = t^2(3-2t)
            i.e. x(t) = a(t) + (b(t)-a(t))u(t)
            x'(t) = a'(t) + u(t)(b'(t)-a'(t)) + (b(t)-a(t))u'(t), where u'(t)=6t-6t^2 = 6t(1-t)
            x'    = a' +  (t^2(3-2t)) (b'-a') + (b-a)(6t(1-t))
            */
            let u = t * t * (3 - 2 * t);
            let up = 6*t*(1-t);
            let x = a.to(b, u);
            v[i] = {x:x};
            // Compute rotation from the left (a1)
            if (i!==0) {
                // a'(t) is this vector
                let ap=A.segs[i-1].path.endTangent().vector().times(LEN);
                let bp=B.segs[i-1].path.endTangent().vector().times(LEN);
                // x'(t)
                let xp = ap.plus( bp.minus(ap).times(u) ).plus(b.minus(a).times(up));
                // This would be the numerical estimation way to do it
                // let tme = t-0.001;
                // let ume = tme * tme * (3-2*tme);
                // let ape = A.pointAt(tme);
                // let bpe = B.pointAt(tme);
                // let da = ape.minus(a).times(1/0.001);
                // let db = bpe.minus(b).times(1/0.001);
                // let xme = ape.to(bpe, ume);
                // xp = x.minus(xme);
                v[i].xp1=new Angle(xp);
            }
            if (i!==ts.length-1) {
                let ap=A.segs[i].path.startTangent().vector().times(LEN);
                let bp=B.segs[i].path.startTangent().vector().times(LEN);
                let xp = ap.plus( bp.minus(ap).times(u) ).plus(b.minus(a).times(up));
                v[i].xp2=new Angle(xp);
            }
            // console.log("i="+i+"\n"+JSON.stringify(v[i]))
        }
        let newSegs:Piece[]=[]
        for (let i=1;i<ts.length;i++) {
            let t1 = ts[i-1];
            let t2 = ts[i];
            let x1 = v[i-1].x;
            let x2 = v[i].x;
            let a1 = v[i-1].xp2;
            let a2 = v[i].xp1;
            let path = this.segs[i-1].path.bend(x1, a1, a2, x2, stretch);
            if (path !== singularity) newSegs.push({path,t1,t2});
        }
        return newSegs.length ? Chain.of(newSegs) : singularity;
    }
    
    // this is a little strange because we might have ChainSegments holding Chains(?)
    // but that's just a convenience for the return value.
    _splitNatural(): [Piece, Piece]|singularity {
        if (this.segs.length === 1) {
            return this.segs[0].path.splitNatural();
        }
        const mid = Math.ceil(this.segs.length / 2);
        let c1 = (this.segs.slice(0, mid));
        let c2 = (this.segs.slice(mid));
        let t = c2[0].t1;
        return [{path: Chain.of(c1), t1:0, t2:t},
                {path: Chain.of(c2), t1:t, t2:1}];
    }

    // for debugging chains
    innerPointsOfNaturalInterest(): Point[] {
        if (this.segs.length === 1) return [];
        return this.segs.slice(1).map(seg => seg.path.start());
    }

    split2(u: number): [Path|singularity, Path|singularity] {
        if (u < 0) u = 0;  // out of range but we're being lenient for rounding errors
        if (u > 1) u = 1;  // ditto
        let a: Piece[] = [];
        let b: Piece[] = this.segs.slice(); // copy
        while (b.length && b[0].t1 < u ) {
            let ss = b.shift();
            if (ss.t2 < u) {
                // we haven't scraped enough
                a.push(ss);
                continue;
            }
            // Now either ss.t2=u (we found the exact split)
            // or ss.t2 > u (we need to split the current sub-path) 
            if (Math.abs(ss.t2-u)<PRECISION_EPSILON) {
                // unlikely to be so exact? maybe not if we're
                // splitting at 0.5 for a 2-element chain.
                // Now ss should belong on the left side 
                // and we have the exact result.
                a.push(ss);
                break;
            }
            // it's too much, we have to break it between ss.t1 and ss.t2
            let splitRatio = (u-ss.t1)/(ss.t2-ss.t1);
            splitRatio = Math.min(1, Math.max(0, splitRatio)); // this is just for rounding errors
            let [left, right] = ss.path.split2(splitRatio);
            if (left === singularity && right === singularity) {
                // Well this sucks.  It means an tiny sub-path
                // was split into tinier singularity subpaths.  
                // If we just ignore it, we might end up with a tiny gap.
                // Let's just not split it.
                a.push(ss); 
                break;
            } else if (left === singularity) {
                b.unshift(ss);
                break;
            } else if (right === singularity) {
                a.push(ss);
                break;
            }
            // We got a real split, at a point between ss.t1 and ss.t2 (i.e. at u)
            // Push the left half onto a, and the right half onto b
            a.push({path:left, t1:ss.t1, t2:u})
            b.unshift({path:right, t1:u, t2:ss.t2})
            break;
        }
        // Note invoking Chain.of() will re-normalize the parameters
        return [a.length ? Chain.of(a) : singularity,
                b.length ? Chain.of(b) : singularity];
    }

    startTangent(): Angle {
        return this.segs[0].path.startTangent();
    }

    endTangent(): Angle {
        return this.segs[this.segs.length - 1].path.endTangent();
    }
}