import {Bounds} from "../Bounds";
import {Path} from "./Path";
import {Shape, shapeColorFunctionByMedianArea} from "./Chain";

// todo, local storage compression
// https://chat.openai.com/chat/03c5e364-6f07-4e25-b274-af68833bd945

export function exportDocumentWithPaths(paths:Path[], vb:Bounds):string {
    let svg = exportPrefix(vb);
    let v = Object.values(paths).map(path=>"\n"+`<path d="${path.fullSvgPath()}" style="fill:none;stroke:black;stroke-width:1px;"/>`);
    v.push(exportSuffix())
    return svg.concat(...v);
}
export function exportDocumentWithShapes(shapes:Shape[], vb:Bounds):string {
    let svg:string[] = [exportPrefix(vb)];
    let shapeColorFunction = shapeColorFunctionByMedianArea(shapes);
    let v: [Path,string][] = shapes.map(s=>[s.toChain(), shapeColorFunction(s)]);
    let w = v.map(x=>`\n<path d="${x[0].fullSvgPath()}" style="fill:${x[1]};stroke:black;stroke-width:1px;"/>`);
    svg = svg.concat(...w);
    svg.push(exportSuffix());
    
    return svg.join("");
}


export function exportPrefix(vb:Bounds) {

    return  `<?xml version="1.0" encoding="UTF-8" standalone="no"?>
<!DOCTYPE svg PUBLIC "-//W3C//DTD SVG 1.1//EN" "http://www.w3.org/Graphics/SVG/1.1/DTD/svg11.dtd">
<svg width="100%" height="100%" viewBox="${vb.toViewBox()}" version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" xml:space="preserve" xmlns:serif="http://www.serif.com/" style="fill-rule:evenodd;clip-rule:evenodd;stroke-linecap:round;stroke-linejoin:round;stroke-miterlimit:1.5;">`;

}
export function exportSuffix() {
    return '</svg>'
}

