import {Bounds} from "../Bounds";
import {BACK_SVG} from "../Math/const";
import {Path, singularity} from "../Math/Path";
import {Segment} from "../Math/Segment";
import {Point} from "../Math/Point";
import {Cubic} from "../Math/Cubic";
import {Chain} from "../Math/Chain";

require("../flatten.js")

export interface ImportedSVG{
    paths:Path[],
    viewBox:Bounds,
    // background image etc
    
}

/**
 * Insert the element into a dummy SVG container,
 * invoke the flatten library on it, parse all the path 'd' attributes
 * from it, and return the parsed Paths and viewBox.  The dummy SVG container
 * is cleaned up.
 */
export function scrapePathsFromParsedDom(svgDocumentLement: HTMLElement, breakChains:boolean=true) :ImportedSVG{
    
    let backContainer = document.getElementById('orig-svg-container')!
    while (backContainer.firstChild) backContainer.removeChild(backContainer.firstChild);
    // let svgDocumentLement: HTMLElement = new DOMParser().parseFromString(uploadedSvg, "image/svg+xml").documentElement;
    backContainer.appendChild(svgDocumentLement);
    
    // Flatten the original SVG; the library does this in place.
    // @ts-ignore
    window.flatten(backContainer.getElementsByTagName("svg")[0], true, true);

    // Grab the viewBox and fix the width,height
    let backSvg=backContainer.getElementsByTagName("svg")[0];
    // could be null; todo support that later
    let originalViewBox = Bounds.forViewBox(backSvg.getAttribute("viewBox") || "0 0 500 500");
    backSvg.setAttribute("width","100%");
    backSvg.setAttribute("height","100%");
    backSvg.setAttribute("id", BACK_SVG);

    let paths:Path[] = pullPathDs(backContainer, breakChains); 
    
    //while (backContainer.firstChild) backContainer.removeChild(backContainer.firstChild);

    return {paths: paths,viewBox:originalViewBox};
}

function pullPathDs(fileContainer: HTMLElement, breakChains:boolean=true):Path[] {
    let all:Path[] = [];
    const pathElements = fileContainer.getElementsByTagName("path");
    for (let i = 0; i < pathElements.length; i++) {
        let d = pathElements[i].getAttribute("d");
        if (!d) continue;
        let paths:Path[] = parsePaths(d);
        if (!breakChains && paths.length>1) {
            all.push(Chain.of(paths.map(p => ({path: p, t1: 0, t2: 1}))));
        } else {
            all.push(...paths);
        }
    }
    return all;
}

function parsePaths(d:string): Path[] {
    let pathSegments = d.split(/(?=[MmLlHhVvCcSsQqTtAaZzn])/);
    let pieces: Path[] = [];
    let gather=(p:(Path|singularity)) => { if (p!==singularity) pieces.push(p); }
    // cursor
    let c = {x: 0, y: 0};
    let subpathStart = {x: 0, y: 0};
    let prevControl = null;
    for (let i = 0; i < pathSegments.length; i++) {
        let segment = pathSegments[i];
        let command = segment[0];
        let args: string[] = segment.substring(1).trim().split(/[\s,]+/);
        switch (command) {
            case 'n':  // none
                break;
            case 'M':
                subpathStart={x:c.x, y:c.y};
                c.x = parseFloat(args.shift()!);
                c.y = parseFloat(args.shift()!);
                while (args.length>=2) {
                    let x2 = parseFloat(args.shift()!);
                    let y2 = parseFloat(args.shift()!);
                    gather(Segment.of(Point.of(c.x, c.y), Point.of(c.x = x2, c.y = y2)));
                }
                prevControl = null;
                break;
            case 'm':
                subpathStart={x:c.x, y:c.y};
                c.x += parseFloat(args[0]);
                c.y += parseFloat(args[1]);
                while (args.length>=2) {
                    let x2 = parseFloat(args.shift()!);
                    let y2 = parseFloat(args.shift()!);
                    gather(Segment.of(Point.of(c.x, c.y), Point.of(c.x += x2, c.y += y2)));
                }
                prevControl = null;
                break;
            case 'Z': case 'z': {
                let x2 = subpathStart.x;
                let y2 = subpathStart.y;
                gather(Segment.of(Point.of(c.x, c.y), Point.of(c.x = x2, c.y = y2)));
                subpathStart = {x:c.x, y:c.y};
                }
                prevControl = null;
                break;
            case 'L':
                while (args.length>=2) {
                    let x2 = parseFloat(args.shift()!);
                    let y2 = parseFloat(args.shift()!);
                    gather(Segment.of(Point.of(c.x, c.y), Point.of(c.x = x2, c.y = y2)));
                }
                prevControl = null;
                break;
            case 'l':
                while (args.length>=2) {
                    let x2 = parseFloat(args.shift()!);
                    let y2 = parseFloat(args.shift()!);
                    gather(Segment.of(Point.of(c.x, c.y), Point.of(c.x += x2, c.y += y2)));
                }
                prevControl = null;
                break;
            case 'H':
                while (args.length) gather(Segment.of(Point.of(c.x, c.y), Point.of(c.x = parseFloat(args.shift()!), c.y)));
                prevControl = null;
                break;
            case 'h':
                while (args.length) gather(Segment.of(Point.of(c.x, c.y), Point.of(c.x += parseFloat(args.shift()!), c.y)));
                prevControl = null;
                break;
            case 'V':
                while (args.length) gather(Segment.of(Point.of(c.x, c.y), Point.of(c.x, c.y = parseFloat(args.shift()!))));
                prevControl = null;
                break;
            case 'v':
                while (args.length) gather(Segment.of(Point.of(c.x, c.y), Point.of(c.x, c.y += parseFloat(args.shift()!))));
                prevControl = null;
                break;
            case 'C': 
                while (args.length >=6) {
                    let c1 = Point.of(parseFloat(args.shift()!), parseFloat(args.shift()!));
                    let c2 = Point.of(parseFloat(args.shift()!), parseFloat(args.shift()!));
                    let p2 = Point.of(parseFloat(args.shift()!), parseFloat(args.shift()!));
                    gather(Cubic.of(Point.of(c.x, c.y), c1, c2, p2));
                    prevControl = c2;
                    c={x:p2.x,y:p2.y};
                }
                break;

            case 'c':
                while (args.length >=6) {
                    let c1 = Point.of(c.x + parseFloat(args.shift()!), c.y + parseFloat(args.shift()!));
                    let c2 = Point.of(c.x + parseFloat(args.shift()!), c.y + parseFloat(args.shift()!));
                    let p2 = Point.of(c.x + parseFloat(args.shift()!), c.y + parseFloat(args.shift()!));
                    gather(Cubic.of(Point.of(c.x, c.y), c1, c2, p2));
                    prevControl = c2;
                    c={x:p2.x,y:p2.y};
                }
                break;
            case 'S':
                while (args.length >=4) {
                    let c1 = prevControl ? Point.of(prevControl.x, prevControl.y) : Point.of(c.x, c.y);
                    let c2 = Point.of(parseFloat(args.shift()!), parseFloat(args.shift()!));
                    let p2 = Point.of(parseFloat(args.shift()!), parseFloat(args.shift()!));
                    gather(Cubic.of(Point.of(c.x, c.y), c1, c2, p2));
                    prevControl = c2;
                    c={x:p2.x,y:p2.y};
                }
                break;
            case 's':
                while (args.length >=4) {
                    let c1 = prevControl ? Point.of(prevControl.x, prevControl.y) : Point.of(c.x, c.y);
                    let c2 = Point.of(c.x + parseFloat(args.shift()!), c.y + parseFloat(args.shift()!));
                    let p2 = Point.of(c.x + parseFloat(args.shift()!), c.y + parseFloat(args.shift()!));
                    gather(Cubic.of(Point.of(c.x, c.y), c1, c2, p2));
                    prevControl = c2;
                    c={x:p2.x,y:p2.y};
                }
                break;
                // todo, quadratics and ellipticals
                // https://www.w3.org/TR/SVG/paths.html#PathDataLinetoCommands
            case 'a': case 'A': case 'Q': case 'q': case 'T': case 't':
                throw new Error("Path elements a,q,t not supported");                    
        }
    }
    
    return pieces;
}

