import {P} from "../Bounds";
import {equalsfp, PRECISION_EPSILON} from "./const";


export class Point implements P {
    static IdReg : Record<string,string>={};
    static IdSeq : number=0;
    readonly x: number;
    readonly y: number;
    private _id: string;
    private _svg:string;

    static of(x:number, y:number):Point {
        return new Point(x,y);
    }
    protected constructor(x: number, y: number) {
        this.x = x;
        this.y = y;
    }

    static forP(p: P): Point {
        return p instanceof Point ? p : Point.of(p.x, p.y);
    }
    hash():string {
        let h=0;
        let hasher=this.svg();
        for (let i=0;i<hasher.length;i++) {
            h = (31*h+hasher.charCodeAt(i))%100;
        }
        return (h%1000).toString(36);
    }
    id(): string {
        if (!this._id) this._id=Point.IdReg[this.svg()];
        if (!this._id) this._id=Point.IdReg[this.svg()]= `P${(Point.IdSeq++).toString(36)}.${this.hash()}`;
        return this._id;
    }

    svg():string {
        if (!this._svg) this._svg = this.x+","+this.y;
        return this._svg;
    }
    hString(): string {
        return this.id()
        // +`(${this.x},${this.y})`;
    }

    equals(other: P): boolean {
        return other===this || other && other.x===this.x && other.y===this.y;
    }

    // distance squared
    d2(other: P): number {
        let dx = (this.x - other.x);
        let dy = (this.y - other.y);
        return dx * dx + dy * dy;
    }

    dist(other: P): number {
        return Math.sqrt(this.d2(other));
    }
    
    // Equals ignoring rounding errors
    equalsfp(other:P) {
        return equalsfp(this.x,other.x) && equalsfp(this.y,other.y)
    }
    
    distxy(x:number,y:number):number {
        let dx=this.x-x;
        let dy=this.y-y;
        return Math.sqrt(dx*dx+dy*dy);
    }

    to(P: Point, u: number): Point {
        return Point.of(this.x + (P.x - this.x) * u,
                         this.y + (P.y - this.y) * u);
    }

    plus(v:Vector): Point {
        return Point.of(this.x + v.dx, this.y + v.dy);
    }

    minus(p: P): Vector {
        return new Vector(this.x - p.x, this.y - p.y);
    }
    
}

export class Vector {
    constructor(public dx: number, public dy: number) {}
    hString():string {
        return `<${this.dx},${this.dy}>`
    }
    abs(): number {
        return Math.sqrt(this.dx ** 2 + this.dy ** 2);
    }
    atan(): number {
        return Math.atan2(this.dy, this.dx);
    }
    plus(other: Vector): Vector {
        return new Vector(this.dx + other.dx, this.dy + other.dy);
    }
    minus(other: Vector): Vector {
        return new Vector(this.dx - other.dx, this.dy - other.dy);
    }
    times(scalar:number): Vector {
        return new Vector(this.dx*scalar, this.dy*scalar);
    }
    toLength(scalar: number): Vector {
        const magnitude = this.abs();
        return magnitude === 0 ? new Vector(0, 0)
                               : new Vector(this.dx * scalar / magnitude, 
                                            this.dy * scalar / magnitude);
    }
    rotate(angle:Angle):Vector {
        return this.timesComplex(angle.vector());
    }
    // Complex number multiplication
    timesComplex(other: Vector): Vector {
        return new Vector(this.dx * other.dx - this.dy * other.dy,
                          this.dx * other.dy + this.dy * other.dx);
    }
    divideComplex(other: Vector): Vector {
        const d = other.abs() ** 2;
        return new Vector((this.dx * other.dx + this.dy * other.dy) / d,
                          (this.dy * other.dx - this.dx * other.dy) / d);
    }
    unit(): Vector {
        return this.toLength(1);
    }
    dot(other: Vector): number {
        return this.dx * other.dx + this.dy * other.dy;
    }
    cross(other: Vector): number {
        return this.dx * other.dy - this.dy * other.dx;
    }
}

export class Angle {
    static PI:Angle = new Angle(new Vector(-1,0));
    private readonly v:Vector;
    
    constructor(v:Vector) {
        this.v=v.unit();
    }
    
    static forAngle(theta:number) {
        return new Angle(new Vector(Math.cos(theta), Math.sin(theta)));
    }
    hString() {
        return String(''+Math.round(360*this.radians()/2/Math.PI)+"");
    }
    radians():number {
        return this.v.atan();
    }
    // Same as angle+PI
    reverse():Angle {
        return new Angle(this.v.times(-1));
    }
    plus(other:Angle):Angle {
        return new Angle(this.v.timesComplex(other.v));
    }
    minus(other:Angle):Angle {
        return new Angle(this.v.divideComplex(other.v));
    }
    times(scalar:number):Angle {
        return Angle.forAngle(this.radians() * scalar);
    }
    vector():Vector {
        return this.v;
    }
}