import {Point} from "./Point";
import {Path, singularity} from "./Path";
import {Segment} from "./Segment";
import {Cubic} from "./Cubic";


export function smoothToCurves(points:Point[]): Path[]|singularity {
    // Whack any adjacent equivalent points
    let p = points[0];
    let p2 = [p];
    for (let i=1;i<points.length;i++) {
        let a = points[i];
        if (!a.equalsfp(p)) {
            p2.push(a);
            p=a;
        }
    }
    points = p2;
    if (points.length<2) return singularity;
    if (points.length===2) {
        // we already checked for singularity
        return [Segment.of(points[0],points[1])] as Segment[];
    } 
    let curves:(Cubic|Segment)[]=[];
    for (let i=1;i<points.length;i++) {
        let a = i===1 ? null : points[i-2];
        let b = points[i-1];
        let c = points[i];
        let d = i===points.length-1 ? null : points[i+1];
        let s = b.dist(c)/3; // length of control points
        let p,q:Point;  // control points
        if (!a || a.equalsfp(c)) { // to prevent dbz
            p = b;
        } else {
            p = b.plus(c.minus(a).toLength(s));
        }
        if (!d || d.equalsfp(b)) {
            q = c;
        } else {
            q = c.plus(b.minus(d).toLength(s));
        }
        curves.push(Cubic.of(b,p,q,c) as Cubic|Segment);
    }
    return curves;
}

export function RamerDouglasPeuckerSmoothing(points: Point[], tolerance: number): Point[] {
    if (points.length <= 2) {
        return points;
    }
    let maxDistance = 0;
    let index = 0;
    for (let i = 1; i < points.length - 1; i++) {
        const distance = perpendicularDistance(points[i], points[0], points[points.length - 1]);
        if (distance > maxDistance) {
            maxDistance = distance;
            index = i;
        }
    }
    if (maxDistance > tolerance) {
        const left = points.slice(0, index + 1);
        const right = points.slice(index);
        const simplifiedLeft = RamerDouglasPeuckerSmoothing(left, tolerance);
        const simplifiedRight = RamerDouglasPeuckerSmoothing(right, tolerance);
        return simplifiedLeft.slice(0, simplifiedLeft.length - 1).concat(simplifiedRight);
    } else {
        return [points[0], points[points.length - 1]];
    }
}

// Calculate the perpendicular distance from a point to a line
function perpendicularDistance(point: Point, lineStart: Point, lineEnd: Point): number {
    const dx = lineEnd.x - lineStart.x;
    const dy = lineEnd.y - lineStart.y;
    const mag = dx * dx + dy * dy;
    const u = ((point.x - lineStart.x) * dx + (point.y - lineStart.y) * dy) / mag;
    const x = lineStart.x + u * dx;
    const y = lineStart.y + u * dy;
    const dx1 = x - point.x;
    const dy1 = y - point.y;
    return Math.sqrt(dx1 * dx1 + dy1 * dy1);
}