import React, {useEffect} from "react";
import {StateContext} from "../App";
import "./Toolbar.css"
import {
    DISTORTION_ORTHORGONAL, DISTORTION_STRETCHY,
    END_TANGENTS_FIXED,
    END_TANGENTS_FLOPPY,
    END_TANGENTS_FREE
} from "../../Math/DragOptions";

export const Edit = () => {
    return <>
        <EditToolSelector/>
    </>
}
export const UndoButton = () => {
    // UNDO:  https://chat.openai.com/chat/863401a7-5788-4d6d-b8e0-299504e1b0fa
    // keyboard handler: https://chat.openai.com/chat/ea8ca9ff-06fa-491d-8793-1d4c4e84c65a
    const {state, setState} = React.useContext(StateContext);

    function undo() {
        let u = state.undo.slice();
        if (u.length) {
            let mos = u.pop();
            setState({...state, mos: mos, undo: u})
        }
    }
    function undoKeyHandler(event:KeyboardEvent) {
        // @ts-ignore
        let tagName = event.target.tagName?.toLowerCase() || "none";

        if (event.key === 'z') {
            let isTextField = ['input', 'textarea', 'select'].indexOf(tagName) > -1;
            if (!isTextField) {
               undo(); 
            }
        }
    }

    useEffect(() => {
        window.addEventListener('keydown', undoKeyHandler);
        return () => {
            window.removeEventListener('keydown', undoKeyHandler);
        };
    }, [state,setState]);

    return <>
                    <button className="undoButton" onClick={undo} >[z]<br/>Undo </button>
    </>
}
export const EditToolSelector = () => {
    const {state, setState} = React.useContext(StateContext);

    const handleToolSelection = (tool: string) => {
        setState({...state, toolSelection: tool});
    };

    // "": Curve
    // "": Zig Zag
    return (
        <>
            <div>
                <table className="toolIconSet" width="100%">
                    <tbody>
                    <tr>
                        <td width="33.3%" onClick={() => handleToolSelection("Drag")}
                            className={state.toolSelection === 'Drag' && "toolIcon selected" || "toolIcon"}>
                            <div >
                                <br/>Drag
                            </div>
                        </td>
                        <td width="33.3%" onClick={() => handleToolSelection("Pencil")}
                            className={state.toolSelection === 'Pencil' && "toolIcon selected" || "toolIcon"}>
                            <div>
                                <br/>Pencil
                            </div>
                        </td>
                        <td width="33.3%"  >
                            <div>
                                <UndoButton/>
                                
                            </div>
                        </td>

                    </tr>
                    </tbody>
                </table>
                {state.toolSelection === 'Drag' && <DragOptions/>}
                {state.toolSelection === 'Pencil' && <PencilOptions/>}
            </div>
        </>
    );
};

const DragOptions: React.FC = () => {
    const {state, setState} = React.useContext(StateContext);

    const handleEndTangents = (event: React.ChangeEvent<HTMLSelectElement>) => {
        // @ts-ignore
        setState({ ...state, dragOptions:{...state.dragOptions, endTangents:event.target.value} });
    };

    const handleDistortion = (event: React.ChangeEvent<HTMLSelectElement>) => {
        // @ts-ignore
        setState({ ...state, dragOptions:{...state.dragOptions, distortion:event.target.value} });
    };

    return <>
        <table width="100%" border={0} cellPadding={0} cellSpacing={0}>
            <tbody>
            <SnappingSlider/>
            <tr>
                <td className="labelCell">
                    Scaling:
                </td>
                <td  className="controlCell" >
                    <select value={state.dragOptions.distortion} onChange={handleDistortion}>
                        <option value={DISTORTION_STRETCHY}>Auto (Stretchy)</option>
                        <option value={DISTORTION_ORTHORGONAL}>Rigid</option>
                    </select>
                </td>
            </tr>
            <tr>
                <td className="labelCell">
                    End tangents:
                </td>
                <td  className="controlCell" >
                    <select value={state.dragOptions.endTangents} onChange={handleEndTangents}>
                        <option value={END_TANGENTS_FLOPPY}>Auto (Smart)</option>
                        <option value={END_TANGENTS_FIXED}>Fixed Angle</option>
                        <option value={END_TANGENTS_FREE}>Free Rotating</option>
                    </select>
                </td>
            </tr>
            </tbody>
        </table>
        <hr/>
        <div className={'small'}>
            <p><b>Snapping:</b> strength of magnetic snapping.</p>
            <p><b>Rescaling, End Tangents:</b> These options affect smart redrawing of paths when an endpoint is dragged.  You can also use <b>Shift</b> and <b>Control</b> while dragging to toggle these.</p>
        </div>
    </>;
};
const PencilOptions: React.FC = () => {

    return (
        <>
            <div>
                <table width="100%" border={0} cellPadding={0} cellSpacing={0}>
                    <tbody>
                    <SnappingSlider/>
                    <PencilSimplifyingSlider/>
                    </tbody>
                </table>
            </div>
            <hr/>
            <div className={'small'}>
                <p><b>Snapping:</b> strength of magnetic snapping.</p>
                <p><b>Smoothing:</b> Reduce Smoothing (or zoom in) for drawing finer details.</p>
                <p><b>Pause</b> while dragging to create a corner or to stop without snapping.</p>    
            </div>
        </>
    );
};


const SnappingSlider: React.FC = () => {
    const { state, setState } = React.useContext(StateContext);

    const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        setState({ ...state, snapping:parseInt(event.target.value) });
    };

    return (
        <>
            <tr >
                <td className="labelCell">
                    Snapping: 
                </td>
                <td  className="controlCell" ><input type="range" id="snappingSlider"
                                                     name="snappingSlider"
                                                     min={0} max={20} step={1}
                                                     value={state.snapping}
                                                     onChange={handleChange}/>
                    {/*= {state.snapping}*/}
                </td>
            </tr>
        </>
    );
};


const PencilSimplifyingSlider: React.FC = () => {
    const { state, setState } = React.useContext(StateContext);

    const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        setState({ ...state, pencilSimplifying: parseInt(event.target.value) });
    };

    return (
        <>
            <tr >
                <td className="labelCell">
                    Smoothing: 
                </td>
                <td  className="controlCell" >
                        <input type="range" id="pencilSimplifyingSlider"
                               name="pencilSimplifyingSlider"
                               min={-10} max={10}
                               value={state.pencilSimplifying}
                               onChange={handleChange}/>
                    {/*= {state.pencilSimplifying < 0 ? "less ("+state.pencilSimplifying+")" :state.pencilSimplifying > 0 ? "more ("+state.pencilSimplifying+")" : "default (0)"}*/}
                </td>
            </tr>
        </>
    );
};
