import React, {useState} from 'react';
import {Toolbar} from "./Toolbar/Toolbar";
import {Mos} from "./Mos";
import './App.css';
import {Bounds, P} from "../Bounds";
import {SvgZoomer} from "./SvgZoomer";
import {UserCoo} from "./UserCoo";
import ProgressBar from "./ProgessBar";
import {DISTORTION_STRETCHY, DragOptions, END_TANGENTS_FLOPPY} from "../Math/DragOptions";

//https://chat.openai.com/chat/4e94f4bc-d53f-4632-8cca-9f297d2a07a6

interface ToolEventHandler {
    mouseup:(event: React.MouseEvent<HTMLDivElement>)=>void;
    mouseleave:(event: React.MouseEvent<HTMLDivElement>)=>void;
    mousemove:(event: React.MouseEvent<HTMLDivElement>)=>void;
    mousedown:(event: React.MouseEvent<HTMLDivElement>)=>void;
}

export interface State {
    mos: Mos;
    isAuxMouseDown:boolean;
    auxMouseMoveOrigin:P; // in container coordinates
    coo:UserCoo; // tracks viewPort and parent bounds, gives mapping from SVG user coords
    viewOptions:ViewOptions;
    undo:Mos[];
    toolSelection:string;
    inspectToolSelection:string;
    pencilSimplifying: number;  
    snapping:number; // 0=none
    straightenTolerance: number;  // radians
    dragOptions:DragOptions;
}
export interface ViewOptions{
    viewLabels:boolean,
    viewInners:boolean,
    thickLines:number,
    scaleWithZoom:boolean,
    viewShapes:boolean,
}
const initialState: State = {
    mos:new Mos(),
    isAuxMouseDown:false,
    auxMouseMoveOrigin:{x:0,y:0}, // in container coordinates
    
    coo:new UserCoo(Bounds.forViewBox("0 0 500 500"), null),
    viewOptions:{viewLabels:false,thickLines:2,viewInners:true,scaleWithZoom:true,viewShapes:false},
    dragOptions:{distortion:DISTORTION_STRETCHY, endTangents:END_TANGENTS_FLOPPY},
    straightenTolerance: Math.PI/8,
    undo:[],
    toolSelection:"Drag",
    inspectToolSelection:"Loose Ends",
    pencilSimplifying: 0,
    snapping: 10,
};
export interface Progress {
    progress:number,
    message:string,
    visible:boolean
}
export const nullProgress:Progress = {message:"Loading...",visible:false,progress:0}

export const StateContext = React.createContext<{ state: State; setState: React.Dispatch<React.SetStateAction<State>> }>({state: initialState, setState: () => {}});
export const ProgressContext = React.createContext<{ progress: Progress; setProgress: React.Dispatch<React.SetStateAction<Progress>> }>({progress: nullProgress, setProgress: () => {}});

function App() {
    const [state, setState] = useState(initialState);
    const [progress, setProgress] = useState(nullProgress);
    return (
        <StateContext.Provider value={{ state, setState }}>
            <ProgressContext.Provider value={{ progress, setProgress }}>
            <>
                <div className="main">
                    <div className="svgContainer">
                        { progress.visible &&
                          <div style={{position:"absolute", left:"5px",top:"5px",width:"30em",height:"3em"}}>
                              <ProgressBar progress={progress}/>
                          </div>
                        }
                        <SvgZoomer/>
                    </div>
                    
                </div>
                <div className="sidebar">
                    <Toolbar/>
                </div>
            </>
            </ProgressContext.Provider>
        </StateContext.Provider>
    );
}


export default App;
