import React, {useState} from 'react';
import {StateContext} from "../App";
import {FileInput} from "./FileInput";
import {Edit} from "./Edit";
import {ViewToolOptions} from "./ViewToolOptions";
import {DebugToolOptions} from "./DebugToolOptions";
import {Inspect} from "./Inspect";
import "./Toolbar.css"


export const Toolbar = () => {

    const [selectedTool, setSelectedTool] = useState<string|null>("file");
    // const { state, setState } = React.useContext(StateContext);

    const handleToolSelection = (tool: React.SetStateAction<string | null>) => {
        setSelectedTool(tool);
    };

    return <div id="toolbar">


        <div style={{backgroundColor:"#ffeeee"}}>
            <h1  onClick={()=>handleToolSelection("View")}>
                View
            </h1>
            <div className="toolbarSection">{<ViewToolOptions/>}</div>
        </div>
        <div style={{backgroundColor:"#eeeeff"}}>
            <h1  onClick={()=>handleToolSelection("Edit")}>
                Edit
            </h1>
            <div className="toolbarSection">{<Edit/>}</div>
        </div>

        <div style={{backgroundColor:"#ddFF99"}}>
            <h1  onClick={()=>handleToolSelection("Inspect")}>
                Inspect
            </h1>
            <div className="toolbarSection">{<Inspect/>}</div>
        </div>


        <div style={{backgroundColor:"#eeffee"}}>
            <h1  onClick={()=>handleToolSelection("file")}>
                File
            </h1>
            {/*<div>{selectedTool==='file' && <FileInput/>}</div>*/}
            <div className="toolbarSection">{<FileInput/>}</div>
        </div>
        <div style={{backgroundColor:"#ff88ee"}}>
            <h1  onClick={()=>handleToolSelection("Debug")}>
                Debug
            </h1>
            <div className="toolbarSection">{<DebugToolOptions/>}</div>
        </div>

    </div>;
};


        