import React from "react";
import {StateContext} from "../App";
import {
    DISTORTION_ORTHORGONAL, DISTORTION_STRETCHY,
    END_TANGENTS_FIXED,
    END_TANGENTS_FLOPPY,
    END_TANGENTS_FREE
} from "../../Math/DragOptions";

export const ViewToolOptions = () => {
    const {state, setState} = React.useContext(StateContext);

    const handleThicknessSlider = (event: React.ChangeEvent<HTMLSelectElement>) => {
        setState({...state, viewOptions: {...state.viewOptions, thickLines:parseFloat(event.target.value)}});
    };
    const handleScaleWithZoom = (event: React.ChangeEvent<HTMLInputElement>) => {
        setState({...state, viewOptions: {...state.viewOptions, scaleWithZoom: !state.viewOptions.scaleWithZoom}});
    };
    const toggleViewInners = () => {
        setState({...state, viewOptions: {...state.viewOptions, viewInners: !state.viewOptions.viewInners}});
    };
    const toggleViewShapes = () => {
        setState({...state, viewOptions: {...state.viewOptions, viewShapes: !state.viewOptions.viewShapes}});
    };
    return <div>
        <div className="small">
            These options only affect the display, not the final SVG.
        </div>
        <hr/>
        <div className="controlCell">
            Edge thickness:
            <select value={state.viewOptions.thickLines} onChange={handleThicknessSlider}>
                <option value=".5">0.5px (laser kerf)</option>
                <option value="1">1px</option>
                <option value="2">2px</option>
                <option value="3">3px</option>
                <option value="4">4px</option>
                <option value="5">5px</option>
                <option value="6">6px</option>
                <option value="7">7px</option>
                <option value="8">8px</option>
            </select>
        </div>
        <div className="controlCell"><label>
             Scale with zoom:
            <input type="checkbox" id="scaleWithZoom" name="scaleWithZoom"
                   checked={state.viewOptions.scaleWithZoom} onChange={handleScaleWithZoom}/>
        </label></div>
        <div className="controlCell">
            <label>Make polypaths visible:
                <input type="checkbox" id="viewInners" name="viewInners"
                       checked={state.viewOptions.viewInners} onChange={toggleViewInners}/>
            </label>
        </div>
        <div className="controlCell">
            <label>View 2-D shapes:
                <input type="checkbox" id="viewShapes" name="viewShapes"
                       checked={state.viewOptions.viewShapes} onChange={toggleViewShapes}/>
            </label>
        </div>
    </div>;
};