import React from "react";
import {StateContext} from "../App";
import {exportDocumentWithPaths} from "../../Math/Exporter";

export const DebugToolOptions = () => {
    const {state, setState} = React.useContext(StateContext);
    const toggleViewLabels = () => {
        setState({...state, viewOptions: {...state.viewOptions, viewLabels: !state.viewOptions.viewLabels}});
    };
    const dumpMos = () => {
        console.log(state.mos.hString())
    };
    const dumpSvg = () => {
        let paths = Object.values(state.mos.paths);
        let bounds = paths.map(p => p.bounds()).reduce((a, b) => a.union(b));
        console.log(exportDocumentWithPaths(paths, bounds))
    };
    const dumpView = () => {
        let bounds = state.coo.effectiveViewBox;
        let paths = Object.values(state.mos.paths).filter(path => path.bounds().isInside(bounds));
        console.log(exportDocumentWithPaths(paths, bounds))
    };
    const saveSvg = () => {
        let paths = Object.values(state.mos.paths);
        let bounds = paths.map(p => p.bounds()).reduce((a, b) => a.union(b));
        let doc = exportDocumentWithPaths(paths, bounds);
        localStorage.setItem("clip", doc)
        // navigator.clipboard.writeText(doc);
    };
    const saveView = () => {
        let bounds = state.coo.effectiveViewBox;
        let paths = Object.values(state.mos.paths).filter(path => path.bounds().isInside(bounds));
        let doc = exportDocumentWithPaths(paths, bounds);
        // navigator.clipboard.writeText(doc);
        localStorage.setItem("clip", doc)
    };
    const cropView = () => {
        let bounds = state.coo.effectiveViewBox;
        // let paths = Object.values(state.mos.paths).filter(path=>path.bounds().isInside(bounds));
        let mos = state.mos.clone();
        let undo = [...state.undo];
        undo.push(state.mos);
        let paths = Object.keys(mos.paths);
        for (let key of paths) {
            let path = mos.paths[key];
            if (!path.bounds().isInside(bounds)) mos._remove(path);
        }
        setState({...state, undo: undo, mos: mos});
    }
    const makeChains = () => {
        let mos = state.mos.clone();
        mos.accept([], true);
        setState({...state, mos: mos});
    };
    const fragmentize = () => {
        let mos = state.mos.fragmentizeAll();
        setState({...state, mos: mos});
    };
    const smooth = () => {
        let mos = state.mos.smoothAll();
        setState({...state, mos: mos});
    };
    const linearize = () => {
        let mos = state.mos.linearizeAll();
        setState({...state, mos: mos});
    };
    return <div>
        <div>
            <input type='checkbox' checked={state.viewOptions.viewLabels} onChange={toggleViewLabels}/>
            View Labels (for debugging)
        </div>
        <div>
            <button onClick={dumpMos}>Dump Mos to console</button>
        </div>
        <div>
            <button onClick={cropView}>Crop to viewbox</button>
        </div>
        <div>
            <button onClick={dumpSvg}>Dump all SVG to console</button>
        </div>
        <div>
            <button onClick={dumpView}>Dump viewport SVG to console</button>
        </div>
        <div>
            <button onClick={saveSvg}>Save all SVG to clip</button>
        </div>
        <div>
            <button onClick={saveView}>Save viewport SVG to clipboard</button>
        </div>
        <div>
            <button onClick={makeChains}>Simplify paths (make chains)</button>
        </div>
        <div>
            <button onClick={smooth}>Smooth paths</button>
        </div>
        <div>
            <button onClick={fragmentize}>Fragmentize</button>
        </div>
        <div>
            <button onClick={linearize}>Linearize</button>
        </div>

    </div>;
};