import {StateContext, ViewOptions} from "./App";
import React, {useEffect, useState} from 'react';
import {Mos, POI} from "./Mos";
import {minRenderScale, Preview} from "./SvgZoomer";
import {P} from "../Bounds";
import {Path} from "../Math/Path";


export const RendPoiStyles = React.memo((props: { prec: number}) => {
    return <>
        <style>{`
            circle.poiDotHighlighted {fill:#ffff00; stroke:none; opacity:1; pointer-events:none  }
            circle.poiDotI {fill:black;   stroke:none; opacity:.9; pointer-events:none  }
            circle.poiDot0 {fill:magenta; stroke:none; opacity:.9;     }
            circle.poiDot1 {fill:#FF0000; stroke:none; opacity:.9;     }
            circle.poiDot2 {fill:#000000; stroke:none; opacity:.9; }
            circle.poiDot3 {fill:#000000; stroke:none; opacity:.9; }
            circle.poiAdded {fill:blue; stroke:none; opacity:1; pointer-events:none;}
            .poiDot1b      {fill:#FF0000; stroke:none; opacity:.9;     }
            .poiDot2b      {fill:#00ff00; stroke:none; opacity:.9; }
            .poiDot3b      {fill:#00ff00; stroke:none; opacity:.9; }
            text.poiLabel { font-size:${props.prec*3}px }
            xxx.gpoiDotHover:hover  {opacity:1 }
            xxx.gpoiDotHover  {opacity:0.01}
        `}</style>
        <defs>
            <circle id="defPoiDotHighlighted" r={props.prec*20} className="poiDotHighlighted"/>
            <circle id="defPoiDotI" r={props.prec*0.3} className="poiDotI"/>
            <circle id="defPoiDot0" r={props.prec*0.35} className="poiDot0"/>
            <circle id="defPoiDot1" r={props.prec*1} className="poiDot1"/>  
            <circle id="defPoiDot2" r={props.prec*1}   className="poiDot2"/>
            <circle id="defPoiDot3" r={props.prec*1} className="poiDot3"/>
            <circle id="defPoiAdded" r={props.prec*0.5} className="poiAdded"/>
            <circle id="defPoiDot1b" r={props.prec*1.5} className="poiDot1b"/>
            <circle id="defPoiDot2b" r={props.prec*1.5} className="poiDot2b"/>
            <circle id="defPoiDot3b" r={props.prec*1.5} className="poiDot3b"/>
        </defs>
    </>;
    
},  (prevProps, nextProps) => prevProps.prec === nextProps.prec);


export const RendPreviewHighlights = React.memo((props: {mos: Mos, preview:Preview}) => {

    return <>
        <g id="RendPreviewHighlights">
            {props.preview.highlightedPoints.map(point=>{
                return  <use key={point.id()} xlinkHref={'#defPoiDotHighlighted'}
                             x={point.x} y={point.y}  className={'poiDotHighlighted'}/>
            })}
        </g>
    </>
}, (a, b) => {
    // Return true if nothing has changed
    return (
        a.mos===b.mos 
        && a.preview===b.preview
    );
});


export const RendPois = React.memo((props: {paths:Record<string,Path>, viewOptions:ViewOptions, pois: Record<string,POI>}) => {
    
    // Get the inner points duplicates
    let dedupInners:Record<string,any> = {};
    let inners1 = props.viewOptions.viewInners 
                  ?  Object.values(props.paths)
                    .flatMap(path => path.innerPointsOfNaturalInterest())
                    .filter(path=> dedupInners[path.id()] ? false : (dedupInners[path.id()] = 1))
                  : []
   
    
    return <>
        <g id="points">
            {Object.values(props.pois).map(poi => {
                return <PoiDots key={poi.p.id()} poi={poi} viewOptions={props.viewOptions} hover={false}/>;
            })}
        </g>
        <g id="innerPoints">
            {inners1.map(point=>{
                // return <PoiDots key={poi.p.id()} poi={poi} viewLabels={props.viewLabels}/>;
                // console.log("rendering inner point "+point.id());
                return <use key={point.id()} xlinkHref="#defPoiDotI" x={point.x} y={point.y}  className="poiDotI" />
            })}
        </g>
    </>
}, (a, b) => {
    // Return true if nothing has changed
    return (
        a.pois === b.pois
        && a.viewOptions===b.viewOptions
        && a.paths===b.paths
    );
});

export const PoiDots = React.memo((props: { viewOptions:ViewOptions,poi: POI, hover:boolean}) => {
    let s = props.poi.star.length;
    let x = props.poi.p.x;
    let y = props.poi.p.y;
    let id = props.poi.p.id();
    let identifier = {poid:id};
    let css = s===0 ? "poiDot0" : s === 1 ? "poiDot1" : s === 2 ? "poiDot2" : "poiDot3";
    let link= s===0 ? "#defPoiDot0" : s===1 ? "#defPoiDot1" : s===2 ? "#defPoiDot2" : "#defPoiDot3"
    if (props.hover) {
        return <use xlinkHref={link+"b"} x={x} y={y}  className={css+"b"}  {...identifier}/>
    } else {
        return  <use xlinkHref={link} x={x} y={y}  className={css}  {...identifier}/>;
    }
}, (a, b) => {
    // Return true if nothing has changed
    return (
        a.poi === b.poi 
        && a.viewOptions===b.viewOptions
        && a.hover===b.hover
    );
});