import React from 'react';
import {Mos} from "./Mos";
import {Preview} from "./SvgZoomer";
import {shapeColorFunctionByMedianArea} from "../Math/Chain";


export const RendPathStyles = React.memo( (props: {prec:number}) => {
    return <>
        <defs>
            <marker id="markerSquare" markerWidth="7" markerHeight="7" refX="4" refY="4"
                    orient="auto" markerUnits="strokeWidth">
                <rect x="1" y="1" width="5" height="5" className={'.markerSquare'}/>
            </marker>

            <marker id="markerArrow" markerWidth="13" markerHeight="13" refX="2" refY="7"
                    orient="auto" markerUnits="strokeWidth">
                <path d="M2,2 L2,13 L8,7 L2,2" className={'.markerArrow'} />
            </marker>
        </defs>

        <style>
            {`
            .markerSquare {stroke: none; fill:#000000;}
            .markerArrow {fill: #000000}
            path.pths     {opacity:.5;fill:none;stroke-linecap:round;stroke:black;stroke-width:${props.prec*(1)} }
            path.pthsb    {opacity:.5;fill:none;stroke-linecap:round;stroke:black;stroke-width:${props.prec*(1.5)} }
            path.pths.removed { stroke:red; pointer-events:none; position:relative; z-index:10 }
            path.pths.added { opacity:1;  stroke:blue; pointer-events:none; stroke-dasharray:1,1; stroke-width:${props.prec*0.5}}
            path.pths.added.highlight { opacity:1;  stroke:#FFFF00; pointer-events:none; stroke-width:${props.prec*20}}
            path.pthsFine {opacity:1;fill:none;stroke-linecap:round;stroke:purple;stroke-width:${props.prec*0.1} }
            path.control1 {opacity:1;fill:none;stroke-linecap:round;stroke:green;stroke-width:${props.prec*0.3} } 
            path.control2 {opacity:1;fill:none;stroke-linecap:round;stroke:red;stroke-width:${props.prec*0.3} } 
            xxxpath.pths:hover {stroke-width:${props.prec*(3)}}
            `}
        </style>

    </>;
}, /*props equivalence*/ (a, b) => {
    return (
        a.prec === b.prec
    );
});

export const RendPaths = React.memo( (props: { mos:Mos, preview:Preview}) => {
    // let removed=(k:string)=> props.previewMos && props.mos.paths[k] && !props.previewMos.paths[k]
    return <>
        <g id="paths">
            {Object.entries(props.mos.paths).map(([k, path]) => {
                let identifier = {pathid:k};
                let d = path.fullSvgPath();
                let style = path.debugClass || "pths";
                if (props.preview.diff.left[k]) style = style + " removed";
                return   <g key={k}>
                    <path className={style} d={d} {...identifier}/>
                    
                    
                    
                    <path key={k+"fine"} className="pthsFine" d={path.fragmentize().fullSvgPath()} markerEnd="url(#markerArrow)" {...identifier}/>
                </g>
            })}
        </g>
    </>;
}, /*props equivalence*/ (a, b) => {
    return (
        a.mos === b.mos && a.preview===b.preview
    );
});

export const RendShapes = React.memo( (props: { mos:Mos, prec:number}) => {
    let ss = props.mos.shapes().map(s=>s.estimatedArea()).sort((a,b)=>a-b);
    if (!ss || !ss.length) return <></>;
    let half = Math.floor(ss.length/2);
    let medianSize = ss.length % 2 ? ss[half] : (ss[half-1]+ss[half])/2.0;
    
    let shapeColorFunction = shapeColorFunctionByMedianArea(props.mos.shapes());
    
    return <>
        <style>
            {`
                @keyframes blink {
                    0%, 100% { stroke-opacity: 1; }
                    50% { stroke-opacity: 0; }
                }
            
            path.shape {    
                stroke-width: 0;
                /* For opacity, see shapeColor() in const.ts  */
            }
            path.shapeOutlineHighlighted {
                stroke-width: ${props.prec*2};
                fill:none;stroke-linecap:round;stroke:#ff0088;
                     /*animation: blink 1s linear infinite;*/
            }
            `}
        </style>
        <g id="shapes">
            {props.mos.shapes().map((shape)=>{
                let key = shape.segs[0].id(); 
                let fullPath = shape.fullSvgPath();
                let colorParam = shape.estimatedArea() / medianSize;
                let color=shapeColorFunction(shape);
                let className = "shape " //+( colorParam<0.2 ? "halo" : "");
                
                return <>
                    <path key={key} style={{fill:color}} className={className} d={fullPath}/>
                    { colorParam<0.2 &&
                      <path key={key+"h"}  className={"shapeOutlineHighlighted"} d={fullPath}/> }
                    </>
            })}
        </g>
    </>
}, /*props equivalence*/ (a, b) => {
    return (
        a.mos === b.mos 
    );
});
    
export const RendPreviewPaths = React.memo( (props: { mos:Mos,preview:Preview}) => {
    // let added=(k:string)=> props.previewMos && !props.mos.paths[k] && props.previewMos.paths[k]
    // let allAdded = Object.keys(props.previewMos?.paths||[]).filter(added);
    
    return <>
        <g id="added paths">
            {Object.keys(props.preview.diff.right).map((k) => {
                let path=props.preview.diff.right[k];
                let d = path.fullSvgPath();
                let style = "pths added";
                return   <g key={k+" added"}>
                    <path className={style} d={d} />
                    <use xlinkHref={'#defPoiAdded'} x={path.start().x} y={path.start().y}/>
                    <use xlinkHref={'#defPoiAdded'} x={path.end().x} y={path.end().y}/>
                </g>

            })}
        </g>
    </>;
}, /*props equivalence*/ (a, b) => {
    return (
        a.mos === b.mos && a.preview===b.preview
    );
});


export const RendAddedPathHighlights = React.memo( (props: { mos:Mos,preview:Preview}) => {
    return <>
        <g id="added highlights">
            {props.preview.highlightedPaths.map((path)=> {
                let d = path.fullSvgPath();
                let style = "pths added highlight";
                return  <path key={path.id()+" added"} className={style} d={d} />
            })}
        </g>
    </>;
}, /*props equivalence*/ (a, b) => {
    return (
        a.mos === b.mos && a.preview===b.preview
    );
});
