import {StateContext} from "./App";
import React, {useEffect, useState} from 'react';
import {Point} from "../Math/Point";
import {Segment} from "../Math/Segment";
import {RamerDouglasPeuckerSmoothing, smoothToCurves} from "../Math/RDPSmoothing";
import {PoiDots} from "./RendPois";
import {SnapResult} from "./Mos";


export const HoverInteractions = (props:{container: React.RefObject<HTMLDivElement> ,prec:number}) => {
    
    
    const { state, setState } = React.useContext(StateContext);
    const [ point, setPoint] = useState<SnapResult|null>(null);

    function mousemove(event: MouseEvent) {
        let snapping = state.snapping * Math.max(1, state.coo.pixelRatio());
        let to = Point.forP(state.coo.translateContainerToSvg(event.clientX, event.clientY));
        setPoint(state.mos.getSnapTo(to,snapping));
        
    }
    function mouseleave(event:MouseEvent) {
        setPoint(null);
    }
    useEffect(() => {
        let p: HTMLDivElement = props.container.current;
        p.addEventListener("mousemove", mousemove);
        p.addEventListener("mouseleave", mouseleave);
        return () => {
            p.removeEventListener("mousemove", mousemove);
            p.removeEventListener("mouseleave", mouseleave);
        };
    }, [state, props.container]);

    
    if (!point?.p) return <></>;
    
    return <g style={{pointerEvents:"none"}}>
        <style>{`
                .hoverSnapDot      { fill:#0000FF; stroke:none; opacity:1;     }
            `}</style>
        <defs>
            <circle id="defHoverSnapDot" r={props.prec*1.1} className="hoverSnapDot"/>
        </defs>
        {/*{ point.poi && <PoiDots viewOptions={state.viewOptions} poi={point.poi} hover={true}/> }*/}
        {/*{ point.path && <path className="pthsb" d={point.path.fullSvgPath()} />}*/}
        <use xlinkHref={"#defHoverSnapDot"} x={point.p.x} y={point.p.y}  className={'hoverSnapDot'}/>
    </g>;

};
