import {StateContext, ViewOptions} from "./App";
import React from 'react';
import {Mos, POI} from "./Mos";
import {Point} from "../Math/Point";

export const RendPoiLabels= (props: {viewOptions:ViewOptions, mos:Mos}) => {

    // Make a list of poi that should be visible
    let poiLabels = !props.viewOptions.viewLabels ? [] : Object.values(props.mos.pois);
    let innerLabels:Point[] = !props.viewOptions.viewLabels ? [] : Object.values(props.mos.paths)
        .map(p=>p.innerPointsOfNaturalInterest())
        .flat(1)
        .filter(p=>!props.mos.pois[p.id()]);

    return <>
        <g id="poiLabels" style={{zIndex:1000}}>
            {poiLabels.map(poi =><PoiLabel key={poi.p.id()} poi={poi}/>)}
        </g>
        <g id="innerLabels">
            {innerLabels.map(point =><InnerLabel key={point.id()} p={point}/>)}
        </g>
    </>;
};

const PoiLabel = (props:{poi:POI, })=>{
    // console.log("rendering 1 poi label");
    return <text key={"poiLabel" + props.poi.p.id()} opacity={0.8} fill={"#000000"}
                 x={props.poi.p.x} y={props.poi.p.y} textAnchor="middle"
                 dy="-2em" className={"poiLabel"} fontFamily="sans-serif">
        {props.poi.p.id()
         // +" (" + props.poi.star.length + ") "
        }
    </text>
}
const InnerLabel = (props:{p:Point})=>{
    // console.log("rendering 1 poi inner label");
    return <text key={props.p.id()} opacity={0.8} fill={"#00ff00"} x={props.p.x} y={props.p.y}
                 textAnchor="middle" dy="-2em" className={"poiLabel"} fontFamily="sans-serif">
        {props.p.id()}
    </text>

}