import React, {useEffect} from "react";
import {StateContext} from "./App";
import {Bounds} from "../Bounds";

/**
 * parentRef is the enclosing Div (need to figure out the viewport)
 * svgRef is the <svg. (needed to figure out the viewbox)
 */
export const ZoomInteractions = (props: { 
    parentRef: React.RefObject<HTMLDivElement>, 
    svgRef: React.RefObject<SVGSVGElement>,
    origSvgContainerRef: React.RefObject<HTMLDivElement>}) => {

    const {state, setState} = React.useContext(StateContext);

    const handleResize = () => {
        if (!props.parentRef.current) return;
        let cb = new Bounds(0, 0, props.parentRef.current.clientWidth, props.parentRef.current.clientHeight);
        if (!cb.equals(state.coo.viewPort)) {
            // console.warn("Resizing container: ",cb.toViewBox());
            setState({...state,coo:state.coo.updateViewPort(cb)});
        }
    };

    function updateOrigSvg() {
        let div1Ref = props.parentRef;
        let svg1Ref = props.svgRef;
        // We need to check if our refs are currently referencing something.
        if (div1Ref.current && svg1Ref.current) {
            // Get div1's width and height
            const { clientWidth: divWidth, clientHeight: divHeight } = div1Ref.current;

            // Get svg1's viewBox
            const svgViewBox = svg1Ref.current.getAttribute('viewBox');

            // Apply them to orig-svg-container and OriginalSvg
            const originalDiv = document.getElementById('orig-svg-container');
            const originalSvg = document.getElementById('origSvg');
            if (originalDiv && originalSvg) {
                const rect = div1Ref.current.getBoundingClientRect();

                // Set the width, height and location of OriginalSvgDiv
                originalDiv.style.width = `${divWidth}px`;
                originalDiv.style.height = `${divHeight}px`;
                originalDiv.style.position = 'absolute';
                originalDiv.style.left = `${rect.left}px`;
                originalDiv.style.top = `${rect.top}px`;

                // Set the viewBox of OriginalSvg
                originalSvg.setAttribute('viewBox', svgViewBox);
            }
        }
    }
    React.useEffect(() => {
       updateOrigSvg();
    }, [props.parentRef.current, props.svgRef.current, props.origSvgContainerRef.current, state.coo]);

    function onAuxMouseDown(event: MouseEvent) {
        if (event.button!==1) return;
        setState({...state,
                     isAuxMouseDown:true,
                     auxMouseMoveOrigin:{x:event.clientX,y:event.clientY}});
    }
    function onAuxMouseUp(event: MouseEvent) {
        if (event.button!==1) return;
        setState({...state,
                     isAuxMouseDown:false});
    }
    function onAuxMouseMove(event: MouseEvent) {
        if (!state.isAuxMouseDown) return;
        if (0===(event.buttons & 4)) return;
        let dx = event.clientX - state.auxMouseMoveOrigin.x;
        let dy = event.clientY - state.auxMouseMoveOrigin.y;
        let c = props.parentRef.current;
        let parentViewPort = new Bounds(0, 0, c.clientWidth, c.clientHeight);
        let coo = state.coo.updateViewPort(parentViewPort);
        let sdx = dx * coo.effectiveViewBox.w / coo.viewPort.w;
        let sdy = dy * coo.effectiveViewBox.h / coo.viewPort.h;
        let newViewBox = new Bounds(coo.nominalViewBox.x - sdx, coo.nominalViewBox.y - sdy,
                                    coo.nominalViewBox.w,       coo.nominalViewBox.h);
        setState({...state,
                     coo:coo.updateViewBox(newViewBox),
                     auxMouseMoveOrigin:{x:event.clientX,y:event.clientY}});
    }

    function onWheel(event: WheelEvent) {
        let s = props.svgRef.current;
        let c = props.parentRef.current;
        let parentViewPort = new Bounds(0, 0, c.clientWidth, c.clientHeight);
        let coo = state.coo.updateViewPort(parentViewPort);
        let p = coo.translateContainerToSvg(event.clientX,event.clientY);
        let expansionFactor = (event.deltaY<0) ? 1/1.1 : 1.1;
        let vb:Bounds = coo.nominalViewBox;
        let u = (p.x-vb.x)/vb.w;
        let v = (p.y-vb.y)/vb.h;
        let b2:Bounds = new Bounds(
            vb.x - vb.w * u * (expansionFactor-1),
            vb.y - vb.h * v * (expansionFactor-1),
            vb.w * expansionFactor,
            vb.h * expansionFactor
        )
        setState({...state, coo:state.coo.updateViewBox(b2)});
        event.preventDefault();


    }
    useEffect(() => {
        handleResize();
        let p = props.parentRef.current;
        p.addEventListener("wheel", onWheel);
        p.addEventListener("mousedown", onAuxMouseDown);
        p.addEventListener("mouseup", onAuxMouseUp);
        p.addEventListener("mouseleave", onAuxMouseUp);
        p.addEventListener("mousemove", onAuxMouseMove);
        window.addEventListener('resize', handleResize);
        return () => {
            window.removeEventListener('resize', handleResize);
            p.removeEventListener("wheel", onWheel);
            p.removeEventListener("mousedown", onAuxMouseDown);
            p.removeEventListener("mouseup", onAuxMouseUp);
            p.removeEventListener("mouseleave", onAuxMouseUp);
            p.removeEventListener("mousemove", onAuxMouseMove);
        };
    }, [state]);

    return <></>;

};