var tcount=0;
var tcount2=0;
// assumes endpoints are normalized
// Todo this is really sucky.
function _isNearDuplicateHeuristicNormalized(p1:Path,p2:Path):boolean {
    // console.log("h:"+(tcount++)+","+(tcount2));
    if (p1.equals(p2)) return true;
    if (!p1.start().equals(p2.start())) return false;
    if (!p1.end().equals(p2.end())) return false;
    // console.log("h:"+(tcount)+","+(tcount2++));

    // The case of two paths with the same start and end, but not equal, is rare enough
    // that we can do a little more work.
    if (1 /* more exact method */) {
        let f1 = p1.linearize();
        let f2 = p2.linearize();
        let d1 = f1.length();
        let d2 = f2.length();
        if (Math.abs(d1-d2)>PRECISION_PIXEL) return false;
        for (let t=0;t<d1 && t<d2; t+=PRECISION_PIXEL) {
            if (f1.pointAt(t).dist(f2.pointAt(t))>PRECISION_PIXEL) return false;
        }
        console.log("_isNearDuplicateHeuristicNormalized: near and not equal");
        return true;
    } else { // old cheaper method
        // if (Math.abs(p1.startTangent().minus(p2.startTangent()).a()) > 0.01) return false;
        // if (Math.abs(p1.endTangent().minus(p2.endTangent()).a()) > 0.01) return false;
        let p1s = p1.split2(0.5);
        let p2s = p2.split2(0.5);
        if (p1s[0]===singularity) return false;
        if (p2s[0]===singularity) return false;
        let a = (p1s[0] as Path).end();
        let b = (p2s[0] as Path).end();
        if (a.dist(b)>PRECISION_PIXEL) return false;
        console.log("_isNearDuplicateHeuristicNormalized: near and not equal");
        return true;
    }
    
}

const isNearDuplicateHeuristicNormalized:(p1:Path,p2:Path)=>boolean = memoizeWithWeakMaps2(_isNearDuplicateHeuristicNormalized); 