import {Point} from "./Math/Point";


interface P {
    x:number;
    y:number;

}

class Bounds {
    readonly x:number;
    readonly y:number;
    readonly w:number;
    readonly h:number;
    readonly mx:number;
    readonly my:number

    constructor(x: number, y: number, w: number, h: number) {
        this.x = x;
        this.y = y;
        this.w = (w || 0);
        this.h = (h || 0);
        this.mx = x+w;
        this.my = y+h;
    }

    svgRect(style:string): SVGRectElement {
        let e = document.createElementNS("http://www.w3.org/2000/svg", "rect");
        e.setAttribute("x",String(this.x))
        e.setAttribute("y",String(this.y));
        e.setAttribute("width",String(this.w))
        e.setAttribute("height",String(this.h))
        e.setAttribute("style",style);
        return e;
    }
    static forViewBox(viewBox:string):Bounds {
        if (!viewBox) return null;
        let v = viewBox.trim().replace(/\s+/g, ' ').split(" ");
        let w = v.length>2 ? parseFloat(v[2]) : 0;
        let h = v.length>3 ? parseFloat(v[3]) : 0;
        return new Bounds(parseFloat(v[0]),parseFloat(v[1]),w,h);
    }
    static forPoints(ps:P[]):Bounds {
        return ps.map(p=>new Bounds(p.x,p.y,0,0)).reduce((a,b)=>a.union(b));
    }
    union(other:Bounds):Bounds {
        let x = Math.min(this.x,other.x);
        let y = Math.min(this.y,other.y);
        let mx = Math.max(this.mx,other.mx);
        let my = Math.max(this.my,other.my);
        return new Bounds(x,y,mx-x,my-y);
    }
    
    // The diameter of this.union(other)
    extentWith(other:Bounds):number {
        let x = Math.min(this.x,other.x);
        let y = Math.min(this.y,other.y);
        let mx = Math.max(this.mx,other.mx);
        let my = Math.max(this.my,other.my);
        let dx = mx-x;
        let dy = my-y;
        return Math.sqrt(dx*dx+dy*dy);
    }
    
    toViewBox():string {
        return `${this.x} ${this.y} ${this.w} ${this.h}`;
    }

    /**
     * True if this rectangle intersects the circle of radius r
     * centered on the point
     */
    intersectsCircle(p:Point, r:number):boolean {
        // Closest point on rect to the circle center
        var x = Math.min(this.mx, Math.max(this.x, p.x));
        var y = Math.min(this.my, Math.max(this.y, p.y));
        return p.d2({x:x,y:y})<=r*r;
    }

    intersectsBounds(b: Bounds) {
        let a=this;
        return a.x <= b.mx && a.mx >= b.x
            && a.y <= b.my && a.my >= b.y;
    }
    
    intersectsBoundsFuzzy(b:Bounds, fuzz:number) {
        let a=this;
        return a.x-fuzz <= b.mx && a.mx+fuzz >= b.x
               && a.y-fuzz <= b.my && a.my+fuzz >= b.y;
    }
    /**
     * True iff this rectangle lies entirely inside
     * the circle of radius r centered on the point.
     */
    isInsideCircle(p: Point, r: number) {
        return this.extent(p)<=r;
    }
    /**
     * Return the distance from p to the furthermost part of this box
     */
    extent(p: Point) {
        // let dx = Math.max(p.x-this.x, p.x-this.mx)
        // let dy = Math.max(p.y-this.y, p.y-this.my)
        // return Math.sqrt(dx*dx+dy*dy);
        return Math.max(
            p.dist({x:this.x,y:this.y}),
            p.dist({x:this.x,y:this.my}),
            p.dist({x:this.mx,y:this.y}),
            p.dist({x:this.mx,y:this.my}))
    }

    equals(other: Bounds|null) {
        return other && this.x===other.x && this.y===other.y && this.w===other.w && this.h===other.h;
    }

    isInside(other: Bounds) {
        return this.x>=other.x
               && this.y>=other.y
               && this.mx<=other.mx
               && this.my<=other.my
    }
    size():number {
        return Math.sqrt(this.w*this.w+this.h*this.h);
    }
}

export {Bounds}
export type {P}
