import {StateContext} from "./App";
import React, {useEffect, useState} from 'react';
import {anglePreservingTransformation, PRECISION_PIXEL} from "../Math/const";
import {Point} from "../Math/Point";
import {Segment} from "../Math/Segment";
import {RamerDouglasPeuckerSmoothing, smoothToCurves} from "../Math/RDPSmoothing";
import {Bounds} from "../Bounds";
import {singularity} from "../Math/Path";


interface PencilDrag{points:Point[], lastInteraction:number};

export const PencilInteractions = (props:{container: React.RefObject<HTMLDivElement> ,prec:number}) => {
    
    const CORNER_DELAY=300;
    
    const [ drag0, setDrag] = useState<PencilDrag|null>(null);
    const { state, setState } = React.useContext(StateContext);
    let drag = drag0;

    function begin(start:Point) {
        drag = {points:[start],lastInteraction:Date.now()};
    }
    function move(to:Point, tolerance:number) {
        let svgDist = drag.points[drag.points.length - 1].dist(to);
        if (svgDist >= tolerance)
            drag = {...drag,points:[...drag.points,to]};
        if (svgDist >= tolerance * state.coo.pixelRatio())
            drag = {...drag,lastInteraction:Date.now()};
    }
    // Calls setState
    function end() {
        let ps=drag.points;
        if (ps.length<2) return;

        let scale = 0;
        // for (let i=1;i<ps.length;i++) scale += ps[i-1].dist(ps[i]);
        // scale = Bounds.forPoints(ps).perimeter()/2;
        scale = 100 * state.coo.pixelRatio();
        // let pencilSimplifying=state.pencilSimplifying * Math.max(PRECISION, state.coo.pixelRatio());
        let pencilSimplifying= scale * 0.02 * 2**(state.pencilSimplifying/5);
        console.log("actual pencilSimplifying="+pencilSimplifying);
        
        ps = RamerDouglasPeuckerSmoothing(ps,pencilSimplifying);

        let mos = state.mos.clone();
        // The non-smoothing method...
        // let segs = [];
        // for (let i = 1; i < ps.length; i++) {
        //     segs.push(Segment.of(ps[i - 1], ps[i]));
        // }
        let v = smoothToCurves(ps);
        if (v!==singularity) {
            mos.acceptSync(v, true, true);
            let u = state.undo.slice();
            u.push(state.mos);
            setState({...state,mos:mos, undo:u})
        }
    }
    function mousedown(event: MouseEvent) {
        if (event.button!==0) return;
        let startPoint = Point.forP(state.coo.translateContainerToSvg(event.clientX, event.clientY));
        let snapping = state.snapping * Math.max(1, state.coo.pixelRatio());
        let startSnap = state.mos.getSnapTo(startPoint, snapping).p;
        begin(startSnap);
        setDrag(drag);
    }

    function mouseup(event: MouseEvent) {
        if (event.button!==0) return;
        if (!drag) return;
        let to = Point.forP(state.coo.translateContainerToSvg(event.clientX, event.clientY));
        let snapping = state.snapping * Math.max(1, state.coo.pixelRatio());
        to = state.mos.getSnapTo(to, snapping).p;
        move(to, 0.0);
        end();
        setDrag(null);
    }

    function _mouseMove(to: Point) {
        // Todo, figure out how to make this work
        // let corner = Date.now() > CORNER_DELAY + drag.lastInteraction;
        let corner=false;
        if (corner) {
            move(to, PRECISION_PIXEL);
            end();
            begin(to);
        } else {
            move(to, PRECISION_PIXEL);
        }
        setDrag(drag);
    }

    function mousemove(event: MouseEvent) {
        if (event.button!==0) return;
        if (!drag) return;
        let to = Point.forP(state.coo.translateContainerToSvg(event.clientX, event.clientY));
        _mouseMove(to);
    }
    function mouseleave(event: MouseEvent) {
        if (event.button!==0) return;
        if (!drag) return;
        // cancel it
        setDrag(null);
    }
    
    useEffect(() => {
        let p: HTMLDivElement = props.container.current;
        p.addEventListener("mouseup", mouseup);
        p.addEventListener("mouseleave", mouseleave);
        p.addEventListener("mousemove", mousemove);
        p.addEventListener("mousedown", mousedown);
        return () => {
            p.removeEventListener("mouseup", mouseup);
            p.removeEventListener("mouseleave", mouseleave);
            p.removeEventListener("mousemove", mousemove);
            p.removeEventListener("mousedown", mousedown);
        };
    }, [state,drag, props.container]);

    useEffect(() => {
        let interval: any;

        if (drag?.points?.length) {
            interval = setInterval(() => {
                _mouseMove(drag.points[drag.points.length-1]);
            }, CORNER_DELAY+1);
        }

        return () => {
            clearInterval(interval);
        };
    }, [drag]);


    if (!drag || drag.points.length<2) {
        return <g></g>;
    } else {
        let segs = [];
        let ps = drag.points;
        for (let i = 1; i < ps.length; i++) {
            let s = Segment.of(ps[i - 1], ps[i]);
            if (s!=singularity) segs.push(s);
        }
        return <g>
            <style>{`
                path.drawnPath { opacity:.5;fill:none;stroke-linecap:round;stroke:#4444ff;stroke-width:${props.prec} }
            `}</style>
            { segs.map((seg,idx)=>
                  <path key={idx} className={"drawnPath"} d={seg.fullSvgPath()}/>         
                )}
        </g>; 
        
       
    }
    
    
};
