import {Angle, Point} from "./Point";
import {Bounds, P} from "../Bounds";
import {Segment} from "./Segment";
import {Path, singularity} from "./Path";
import {anglePreservingTransformation, PRECISION_PATH} from "./const";
import {NPoint} from "../Reacts/Mos";
import {DISTORTION_STRETCHY, DistortionOption} from "./DragOptions";

export class Cubic extends Path {
    readonly a: Point;
    readonly b: Point;
    readonly c: Point;
    readonly d: Point;
    private _svg: string;
    private _hString: string;

    static of(a: Point, b: Point, c: Point, d: Point):Segment|Cubic|singularity{
        return Cubic.isSegment(a,b,c,d,PRECISION_PATH) || Cubic._of(a,b,c,d);
    }
    
    static _of(a: Point, b: Point, c: Point, d: Point):Cubic|singularity {
        let cubic = new Cubic(a,b,c,d);
        return cubic.isSingularity() ? singularity : cubic; 
    }
    private constructor(a: Point, b: Point, c: Point, d: Point) {
        super();
        this.a = a;
        this.b = b;
        this.c = c;
        this.d = d;
    }
    svgPath(): string {
        if (!this._svg) this._svg=`C${this.b.x} ${this.b.y} ${this.c.x} ${this.c.y} ${this.d.x} ${this.d.y}`;
        return this._svg; 
    }
    start(): Point {
        return this.a; 
    }
    end(): Point { 
        return this.d; 
    }

    idPrefix():string { return "C" }

    hString(): string {
        if (!this._hString) this._hString= `${this.id()}(${this.a.hString()}~${this.d.hString()})`;
        return this._hString;
    }
    
    _reverse = (): Cubic => {
        return Cubic._of(this.d, this.c, this.b, this.a) as Cubic
    }

    _bounds(): Bounds {
        let x = Math.min(this.a.x, this.b.x, this.c.x, this.d.x);
        let y = Math.min(this.a.y, this.b.y, this.c.y, this.d.y);
        let x2 = Math.max(this.a.x, this.b.x, this.c.x, this.d.x);
        let y2 = Math.max(this.a.y, this.b.y, this.c.y, this.d.y);
        return new Bounds(x, y, x2 - x, y2 - y);
    }

    applyLinearTransform(f: (_: Point) => Point): Cubic|singularity {
        
        return Cubic._of(f(this.a), f(this.b), f(this.c), f(this.d));
    }

    moveRigidtoStartEnd(newStart:Point, newEnd:Point): Cubic|singularity {
        return this.applyLinearTransform(anglePreservingTransformation(this.start(), newStart, this.end(), newEnd))
    }

    // todo, need to recheck for self-intersection?
    _bend(newStart: NPoint, newStartTangent: Angle, newEndTangent: Angle, newEnd: NPoint, stretch: DistortionOption = DISTORTION_STRETCHY): Path|singularity
    {
        let r:Cubic|singularity = this;
        
        // We need this to smooth some degenerate cases, in particular (a near b & c near d)
        let seg = Cubic.isSegment(this.a, this.b, this.c, this.d, PRECISION_PATH);
        if (seg===singularity) return singularity;
        if (seg) {
            r = Cubic._of(this.a, this.a.to(this.d, 0.33), this.d.to(this.a, 0.33), this.d);
        }
        if (r===singularity) return r;

        r = r.moveRigidtoStartEnd(newStart,newEnd);
        if (r===singularity || r.isSingularity()) return singularity;
        // eliminate a degenerate case
        if (r.a.equalsfp(r.b) && r.a.equalsfp(r.c)) {
            // it's actually a segment AD. Make it a nondegenerate cubic.
            r = Cubic._of(r.a, r.a.to(r.d, 0.33), r.d.to(r.a, 0.33), r.d);
            if (r===singularity) return r;
        }
        
        let dA = newStartTangent.minus(r.startTangent());
        // In the degenerate case a=b, we've already guaranteed c!=a; use that vector proportionally to the change.
        let ab = (r.a.equalsfp(r.b))
                 ? r.c.minus(r.a).times(Math.abs(dA.radians()) / Math.PI)
                 : r.b.minus(r.a);
        ab = ab.rotate(dA);
        
        let dD = newEndTangent.reverse().minus(r.endTangent().reverse());
        let dc = (r.d.equalsfp(r.c))
                 ? r.b.minus(r.d).times(Math.abs(dD.radians())/Math.PI)
                 : r.c.minus(r.d);
        dc = dc.rotate(dD);
        // Now we have to return strictly equal newStart and newEnd
        r = Cubic._of(newStart,r.a.plus(ab),r.d.plus(dc),newEnd);
        return r;
    }

    

    isSingularity():singularity|false {
        if (this.a.equalsfp(this.b) && this.b.equalsfp(this.c) && this.c.equalsfp(this.d)) {
            console.log('cubic is a point');
            return singularity;
        }
        return false;
    }
    
    
    /**
     * If the curve with the given control points can be approximated by a 
     * segment to the given precision, return the segment.
     * 
     * Slight issue, I don't think I've guaranteed compatible parameterization
     * in the approximating Segment.  In practice I hope we can get away with that.
     * 
     * If this isn't a singularity but the approximating segment is (i.e. this is a closed loop
     * of size less than given precision), the result will be a singularity.
     * P.S. this doesn't work: https://hcklbrrfnn.files.wordpress.com/2012/08/bez.pdf
     */
    static isSegment(b0:P, b1:P, b2:P, b3:P, precision:number):Segment|singularity|null {
        // If both control points are within 'precision' of the segment between endpoints,
        // then so is the entire convex hull.
        let isStraight = Segment.distancePointToLineSegment(b1,b0,b3)<precision
                      && Segment.distancePointToLineSegment(b2,b0,b3)<precision;
        // console.log("isStraight="+isStraight);
        if (isStraight) {
            return Segment.of(Point.forP(b0), Point.forP(b3));
        } else {
            return null;
        }
    }
    
    //De Casteljau's algorithm
    __split(u:number): [Point,Point,Point,Point,Point,Point,Point] {
        let A = this.a;
        let P = this.b;
        let Q = this.c;
        let G = this.d;
        let B = A.to(P, u);  // B = uP + (1-u)A
                             // B' = P-A
        let F = Q.to(G, u);  // F = uG + (1-u)Q
                             // F' = G-Q
        let X = P.to(Q, u);  // X = uQ + (1-u)P
                             // X' = Q-P
        let C = B.to(X, u);  // C = uX + (1-u)B 
                             // C' = uX' + X + (1-u)B' - B
                             //    = u(Q-P) + uQ + (1-u)P + (1-u)(P-A) - (uP + (1-u)A)
                             //    = u(Q-P+Q-P-P+A-P+A) + P+P-A-A
                             //    = u(2Q-4P+2A) + (2P-2A)
                             // C'' = (2Q-4P+2A)
        let E = X.to(F, u);  // E = uF + (1-u)X
                             // E' = uF' + F + (1-u)X' - X
                             //    = u(G-Q) + uG + (1-u)Q + (1-u)(Q-P) - (uQ + (1-u)P)
                             //    = u(G-Q+G-Q -Q + P - Q + P  ) + Q + Q-P - P
        //     = u(2G-4Q+2P) + (2Q-2P)
        let D = C.to(E, u);  // D = uE + (1-u)C
                             // D' = uE' + E + (1-u)C' - C
                             // D'(0) = E(0) + C'(0) - C(0)
                             //       = X(0) + (2P-2A) - B(0)
                             //       = P + 2P -2A -A
                             //       = 3P-3A
                             //
                             // D'' = uE'' + E' + E' + (1-u)C'' - C' - C'
                             //     = u(E'' -C'') + (2E' +C'' - 2C')
                             // D''(0) = 2E'(0) + C''(0) - 2C'(0)
                            //         = 2(2Q-2P) + (2Q-4P+2A) - 2( 2P-2A )
                            //        = 4Q-4P+2Q-4P+2A-4P+4A
                            //        = 6Q - 12P + 6A
                            //        = 6( (Q-P) + (A-P) )
        
                            // I think we have
                            // D = u^3 G + 3 u^2 (1-u) Q + 3u (1-u)^2 P + (1-u)^3 A
                            // and an upper bound for D' is
                            // |D'(t)|  3(1-t)^2 * |P-A| + 6(1-t)t * |Q-P| + 3t^2 * |G-Q|


                return [A,B,C,D,E,F,G];
    }
    
    // https://math.stackexchange.com/questions/877725/retrieve-the-initial-cubic-bzier-curve-subdivided-in-two-bzier-curves
    split2(u: number):[Segment|Cubic|singularity, Path|singularity] {
        let [A,B,C,D,E,F,G] = this.__split(u);
        return [Cubic.of(A, B, C, D),
                Cubic.of(D, E, F, G)];
    }

    pointAt(t:number):Point {
        let [A,B,C,D,E,F,G] = this.__split(t);
        return D;
    }

    startTangent(): Angle {
        let v = this.b.minus(this.a);
        if (v.abs()===0) v = this.c.minus(this.a);
        if (v.abs()===0) v = this.d.minus(this.a);
        if (v.abs()===0) {
            console.log("degenerate cubic, no tangent")
            return Angle.forAngle(0);
        }
        return new Angle(v);
    }

    endTangent(): Angle {
        return this.reverse().startTangent().reverse();
    }
}