// To understand the coordinates and effective viewBox 
// for a user event.  Every time you instantiate this
// you will get a snapshot of the current viewBox
// and container size, with associated transforms.
import {Bounds, P} from "../Bounds";
import {Point} from "../Math/Point";

export class UserCoo {
    effectiveViewBox: Bounds;
    nominalViewBox: Bounds;
    viewPort: Bounds|null;

    constructor(viewBox: Bounds, parentViewPort: Bounds) {
        this.viewPort = parentViewPort;
        this.nominalViewBox = viewBox;
        let sar = this.nominalViewBox.w / this.nominalViewBox.h;
        let car = this.viewPort ? this.viewPort.w / this.viewPort.h : sar;
        if (car > sar) {
            // container is wider.  effective viewBox must be widened.
            let newBWidth = this.nominalViewBox.h * car;
            let dw = newBWidth - this.nominalViewBox.w;
            this.effectiveViewBox = new Bounds(this.nominalViewBox.x - dw / 2, this.nominalViewBox.y,
                                               this.nominalViewBox.w + dw, this.nominalViewBox.h);
        } else {
            // effective viewBox must be grown vertically
            let newBHeight = this.nominalViewBox.w / car;
            let dh = newBHeight - this.nominalViewBox.h;
            this.effectiveViewBox = new Bounds(this.nominalViewBox.x, this.nominalViewBox.y - dh / 2,
                                               this.nominalViewBox.w, this.nominalViewBox.h + dh);
        }
    }

    toHumanString():string {
        return `viewPort=${this.viewPort?.toViewBox()}\nnominalViewBox=${this.nominalViewBox.toViewBox()}\neffectiveViewBox=${this.effectiveViewBox.toViewBox()}`
    }
    // ratio of  viewport pixel size to SVG pixel size. When zoomed out, will be large.
    pixelRatio() {
        return this.viewPort ? this.effectiveViewBox.w / this.viewPort.w : null;
    }

    translateContainerToSvg(x: number, y: number): Point {
        if (!this.viewPort) return Point.of(x,y);
        let x2 = this.effectiveViewBox.x + this.effectiveViewBox.w * (x / this.viewPort.w);
        let y2 = this.effectiveViewBox.y + this.effectiveViewBox.h * (y / this.viewPort.h);
        return Point.of(x2,y2); 
    }

    updateViewBox(viewBox: Bounds):UserCoo {
        return new UserCoo(viewBox, this.viewPort);
    }
    updateViewPort(parentViewPort:Bounds):UserCoo {
        return new UserCoo(this.nominalViewBox, parentViewPort);
    }
}