import {StateContext} from "./App";
import React, {useEffect, useState} from 'react';
import {Mos} from "./Mos";
import {Point} from "../Math/Point";
import {singularity} from "../Math/Path";
import {dragOptionsWithModifierKeys} from "../Math/DragOptions";
import {PRECISION_PIXEL} from "../Math/const";


interface Drag {
    startTime:number;
    to:Point,
    from:Point,
    pathid:string;
    orig:Mos,
    shiftKey:boolean,
    controlKey:boolean;
};

export const PathsInteractions = (props:{container: React.RefObject<HTMLDivElement> }) => {

    const [ drag, setDrag] = useState<Drag|null>(null);
    const { state, setState } = React.useContext(StateContext);

    function keyup(event:KeyboardEvent) {
        if (!drag) return;

        doDrag(null, event.shiftKey, event.ctrlKey, false)
    }
    function keydown(event:KeyboardEvent) {
        if (!drag) return;
        doDrag(null, event.shiftKey, event.ctrlKey, false)
    }
    function mousedown(event: MouseEvent) {
        if (event.button!==0) return;
        // @ts-ignore
        let id = event.target.getAttribute("pathid");
        if (!id) return;
        let path = state.mos.paths[id];
        if (!path) return;
        let to = state.coo.translateContainerToSvg(event.clientX, event.clientY);
        
        // this doesn't work because we're not even getting
        // the event
        // let snapping = state.snapping * Math.max(1, state.coo.pixelRatio());
        // to = state.mos.getSnapTo(to, snapping).p;
        
        let drag2:Drag = {
            startTime:performance.now(),
            to:to,
            from:to,
            pathid:id, orig:state.mos,
            shiftKey: event.shiftKey,
            controlKey: event.ctrlKey,
        };
        setDrag(drag2);
        let u = state.undo.slice();
        u.push(state.mos);
        setState({...state,undo:u})
    }

// version with snapping
    function mousemove(event: MouseEvent) {
        if (event.button!==0) return;
        if (!drag) return;
        let to = state.coo.translateContainerToSvg(event.clientX, event.clientY);
        doDrag(to, event.shiftKey, event.ctrlKey, false);
    }


    function doDrag(svgCoords: Point | null, shiftKey: boolean, controlKey: boolean, finish: boolean) {
        let drag2 = {...drag, to: svgCoords||drag.to,
            shiftKey: shiftKey, controlKey: controlKey};
        setDrag(drag2);
        let mos = drag2.orig.clone();
        
        let path = mos.paths[drag2.pathid]
        if (path) {
            let delta = Point.forP(drag2.to).minus(drag2.from);
            
            let prec=state.viewOptions.scaleWithZoom
                     ? state.viewOptions.thickLines
                     : state.viewOptions.thickLines*state.coo.pixelRatio();
            let split = path.splitForPoint3(drag2.from, prec);
            
            // We're dragging from the middle, not the ends, so I don't think these
            // cases are possible
            if (!split || split[0]===singularity || split[1]===singularity) return; 

            let dest = split[0].end().plus(delta);
            let loose0 = mos.isLooseEnd(split[0].start());
            let loose1 = mos.isLooseEnd(split[1].end());
            mos._remove(path)
            
            let snap = state.snapping * Math.max(1, state.coo.pixelRatio());
            // don't snap so strongly because it's not a loose endpoint
            snap = snap / 3;
            // compute the snap in a temporary Mos
            if (snap>0) {
                dest = mos.getSnapTo(dest, snap).p;
            }

            let d = dragOptionsWithModifierKeys(state.dragOptions, shiftKey, controlKey);
            split[0] = split[0].autoBend(split[0].start(), dest, loose0, false, d);
            split[1] = split[1].autoBend(dest, split[1].end(), false, loose1, d)

            if (split[0]!==singularity) mos.acceptSyncAndReChain([split[0]])
            if (split[1]!==singularity) mos.acceptSyncAndReChain([split[1]])
            
            setState({...state, mos: mos});
        } else {
        }
    }

    function mouseleave(event: MouseEvent) {
        if (event.button!==0) return;
        if (!drag) return;
        // cancel it
        let u = state.undo.slice();
        let m = u.length ? u.pop() : state.mos;
        setState({...state, mos:m, undo:u})
        setDrag(null);
    }
    function mouseup(event: MouseEvent) {
        if (event.button!==0) return;
        if (!drag) return;
        let to = state.coo.translateContainerToSvg(event.clientX, event.clientY);
        
        // was it a single click with no dragging?  If so, toggle chains.
        if (drag.from.dist(to)<PRECISION_PIXEL && (performance.now()<drag.startTime+250)) {
            setState({...state,
                         mos:state.mos.withToggleChain(drag.pathid)
                         ,undo:[...state.undo, state.mos]});
            setDrag(null);
            return;
        }
        
        doDrag(to, event.shiftKey, event.ctrlKey, true);

        // hammer it?  after we fix the makeChains efficiency problem.
        // let mos = new Mos();
        // let allUnchained = Object.values(state.mos.paths)
        //     .flatMap(path=>path.unchainRecursive())
        //     .map(cs=>cs.path);
        // mos.accept(allUnchained, true);
        // setState({...state,mos:mos});

        setDrag(null);
    }

    useEffect(() => {
        let p: HTMLDivElement = props.container.current;
        p.addEventListener("mouseup", mouseup);
        p.addEventListener("mouseleave", mouseleave);
        p.addEventListener("mousemove", mousemove);
        p.addEventListener("mousedown", mousedown);
        document.addEventListener("keyup", keyup);
        document.addEventListener("keydown", keydown);
        return () => {
            p.removeEventListener("mouseup", mouseup);
            p.removeEventListener("mouseleave", mouseleave);
            p.removeEventListener("mousemove", mousemove);
            p.removeEventListener("mousedown", mousedown);
            document.removeEventListener("keyup", keyup);
            document.removeEventListener("keydown", keydown);
        };
    }, [state,drag, mousedown, mouseleave, mousemove, mouseup, keyup, keydown, props.container]);

    return <></>;

};

