export const DISTORTION_STRETCHY = 'DISTORTION_STRETCHY';
export const DISTORTION_ORTHORGONAL = 'DISTORTION_ORTHORGONAL';
export type DistortionOption = 'DISTORTION_STRETCHY' | 'DISTORTION_ORTHORGONAL';
export const END_TANGENTS_FIXED = 'END_TANGENTS_FIXED';
export const END_TANGENTS_FREE = 'END_TANGENTS_FREE';
export const END_TANGENTS_FLOPPY = 'END_TANGENTS_FLOPPY';
export type EndTangentsOption = 'END_TANGENTS_FIXED' | 'END_TANGENTS_FREE' | 'END_TANGENTS_FLOPPY'

export interface DragOptions {
    /*
    Stretchy vs orthogonal
    fixed tangents, floppy tangents, unfixed tangents 
    */
    distortion: DistortionOption;      // flip with "shift"
    endTangents: EndTangentsOption;  // "control" flips to "Unfixed" or back
}
export function dragOptionsWithModifierKeys(original:DragOptions, shift:boolean, control:boolean): DragOptions {
    let d = {...original};
    if (shift) {
        if (d.distortion === DISTORTION_ORTHORGONAL) {
            d.distortion = DISTORTION_STRETCHY;
        } else if (d.distortion === DISTORTION_STRETCHY) {
            d.distortion = DISTORTION_ORTHORGONAL;
        }
    }
    if (control) {
        if (d.endTangents === END_TANGENTS_FIXED) {
            d.endTangents = END_TANGENTS_FLOPPY;
        } else if (d.endTangents === END_TANGENTS_FREE) {
            d.endTangents = END_TANGENTS_FLOPPY;
        } else if (d.endTangents === END_TANGENTS_FLOPPY) {
            d.endTangents = END_TANGENTS_FREE;
        }
    }
    return d;
}