import React, {useEffect, useRef} from "react";
import {StateContext, ViewOptions} from "./App";
import {Bounds} from "../Bounds";
import {RendAddedPathHighlights, RendPaths, RendPathStyles, RendPreviewPaths, RendShapes} from "./RendPaths";
import { RendPois, RendPoiStyles, RendPreviewHighlights} from "./RendPois";
import {RendPoiLabels} from "./RendPoiLabels";
import {PoisInteractions} from "./PoisInteractions";
import {Mos, MosDiff, POI} from "./Mos";
import {PathsInteractions} from "./PathsInteractions";
import {ZoomInteractions} from "./ZoomInteractions";
import {PencilInteractions} from "./PencilInteractions";
import {HoverInteractions} from "./HoverInteractions";
import {Point} from "../Math/Point";
import {Path} from "../Math/Path";
import {OriginalSvgDocument} from "../Math/const";

export function SvgZoomer() {

    //https://chat.openai.com/chat/4e94f4bc-d53f-4632-8cca-9f297d2a07a6
    const { state, setState } = React.useContext(StateContext);

    const origSvgContainerRef = useRef(null);
    const svgParentRef = useRef(null); // type  React.RefObject<HTMLDivElement>
    const svgRef = useRef(null);


    let prec=state.viewOptions.scaleWithZoom
             ? state.viewOptions.thickLines 
             : state.viewOptions.thickLines*state.coo.pixelRatio();
    // let prec=(state.viewOptions.thickLines ?3:1)* Math.max(1,PRECISION);
    // prec=0.1;
    let viewBox = state.coo.nominalViewBox.toViewBox();
    
    let preview:Preview = {diff:{right:{},left:{},size:0},highlightedPaths:[],highlightedPoints:[]}
    if (state.inspectToolSelection==='Loose Ends') {
        let report = state.mos.looseEndsReport();
        // Adapt the report to the preview interface
        preview = {
            diff: report.autoFix,
            highlightedPaths: Object.values(report.autoFix.left)
                             .concat(Object.values(report.autoFix.right)),
            highlightedPoints: report.autoFix.size>0 ? [] 
                              : report.pois.map(k=>state.mos.pois[k].p)
        }
    }
    if (state.inspectToolSelection==='StraightenIntersections') {
        let report = state.mos.straightenIntersectionsReport(state.straightenTolerance);
        // Adapt the report to the preview interface
        preview = {
            diff: report.autoFix,
            highlightedPaths: Object.values(report.autoFix.left)
                .concat(Object.values(report.autoFix.right)),
            highlightedPoints: []
        }
    }
    if (state.inspectToolSelection==='PointyCorners') {
        let report = state.mos.pointyCornersReport();
        // Adapt the report to the preview interface
        preview = {
            diff: {right:{},left:{},size:0},
            highlightedPaths: [],
            highlightedPoints: report.pois.map(k=>state.mos.pois[k].p)
        }
    }

    if (state.inspectToolSelection==='TangentCorners') {
        let report = state.mos.tangentCornersReport();
        // Adapt the report to the preview interface
        preview = {
            diff: {right:{},left:{},size:0},
            highlightedPaths: [],
            highlightedPoints: report.pois.map(k=>state.mos.pois[k].p)
        }
    }


    // supplements ZoomInteractions.updateOrigSvg for fasater response
    const originalSvg = document.getElementById('origSvg');
    if (originalSvg) {
        originalSvg.setAttribute('viewBox', viewBox);
    }
    
    return <>
        <div ref={origSvgContainerRef} id="orig-svg-container" className="orig-svg-container"/>
        <ZoomInteractions parentRef={svgParentRef} svgRef={svgRef}
                          origSvgContainerRef={origSvgContainerRef}  />
        <div ref={svgParentRef} id="svgParent" className="svgFront">
            <svg ref={svgRef} id="displaySvg" 
                 width="100%" height="100%"
                 viewBox={viewBox} 
                 xmlns="http://www.w3.org/2000/svg">
                
                <SVGBody viewOptions={state.viewOptions} mos={state.mos} prec={prec} preview={preview} />

                {state.toolSelection==='Drag' && <>
                    <PoisInteractions container={svgParentRef}/>
                    <PathsInteractions container={svgParentRef}/>
                </> }
                {state.toolSelection==='Pencil' && <>
                    <PencilInteractions container={svgParentRef} prec={prec}/>
                    <HoverInteractions container={svgParentRef} prec={prec}/>
                </>}
            </svg>
        </div>
    </>;

    
}

export interface Preview {
    diff:MosDiff;
    highlightedPaths:Path[];
    highlightedPoints:Point[];
}

// The portion that doesn't need the viewBox
const SVGBody = React.memo((props: { viewOptions:ViewOptions; mos:Mos,prec:number, preview:Preview}) => {
    return <g>
        {props.viewOptions.viewShapes &&  <RendShapes mos={props.mos} prec={props.prec}/>}
        <RendPathStyles prec={props.prec}/>
        <RendPreviewHighlights mos={props.mos} preview={props.preview}/>
        <RendAddedPathHighlights mos={props.mos} preview={props.preview}/>
        <RendPaths mos={props.mos} preview={props.preview}/>
        <RendPoiStyles prec={props.prec}/>
        <RendPois viewOptions={props.viewOptions} pois={props.mos.pois} paths={props.mos.paths}/>
        <RendPoiLabels viewOptions={props.viewOptions} mos={props.mos}/>
        <RendPreviewPaths mos={props.mos}  preview={props.preview}/>
    </g>
}, /*props equivalence*/ (props1, props2) => {
    return (
        props1.viewOptions === props2.viewOptions
        && props1.mos === props2.mos
        && props1.prec === props2.prec
        && props1.preview === props2.preview
    );
});




