var idSeq:Record<string,number>={};
function nextID(prefix:any):string {
    let sPrefix = String(prefix);
    let r = idSeq[sPrefix] || 0;
    let result = r ? String(sPrefix)+"."+(r.toString(36)) : String(sPrefix);
    r++;
    idSeq[sPrefix]=r;
    return result;
}
export function nextIDFromHash(prefix:string, hasher:string):string {
    let h=0;
    for (let i=0;i<hasher.length;i++) {
        h = (31*h+hasher.charCodeAt(i))%100;
    }
    return nextID(prefix+String(h%100));
}

export interface Piece {
    path: Path;
    // in the owning Chain, parameter start and end for this piece
    t1:number;
    t2:number;
}

export interface Ch {
    segs:Piece[];
}

export const singularity = Symbol("SINGULARITY")
// eslint-disable-next-line @typescript-eslint/no-redeclare
export type singularity = typeof singularity

export abstract class Path {
    // private $splitNatural:WeakRef<[ChainSegment,ChainSegment]>;
    private $splitNatural:[Piece,Piece]|singularity;
    private $fragmentize: Chain; 
    protected $reversed: Path;
    static IdReg:Record<string,string>={};
    static IdSeq : number=0;
    protected _id:string;
    private $linearize: Chain;
    debugClass: string;
    private $bounds: Bounds;
    private $totallyShatter: Path;
    
    abstract start():Point;
    abstract end():Point;
    // the value of the 'd' attribute of a <path> that would draw this path,
    // assuming the pen is already at the start point
    abstract svgPath():string;
    
    fullSvgPath():string {
        return `M${this.start().svg()} ${this.svgPath()}`;
    }
    
    // Two equivalent paths must have the same IDs.
    id():string {
        if (!this._id) this._id=Path.IdReg[this.fullSvgPath()];
        if (!this._id) this._id=Path.IdReg[this.fullSvgPath()]= this.idPrefix() + (Path.IdSeq++);
        return this._id;
    }
    
    abstract idPrefix():string;
    
    // human-readable string;
    abstract hString():string;
    
    
    // heuristic; is it a no-op path within PRECISION_PATH?
    isNoop(precision:number):boolean {
        return this.start().equals(this.end())
               && this.bounds().w<2*precision && this.bounds().h<2*precision;
    }

    /**
     * Get the point at parameter t in [0,1]. Does not normalize points.
     */
    pointAt(t:number):Point {
        if (t<=0) return this.start();
        if (t>=1) return this.end();
        let [a,b] =  this.split2(t);
        if (b!==singularity) return b.start();
        if (a!==singularity) return a.end();
        // can't be split without collapsing.
        return t<0.5 ? this.start() : this.end();
    }

    /**
     * Exact split on parameter in [0,1]. Does not normalize points.
     * May return singulariites on left or right
     */
    abstract split2(t:number):[Path|singularity, Path|singularity];



    /**
     * The impl must use the actual Point objects coming
     * from the transform, not merely equivalent ones,
     * at least for the starting and ending points.
     */
    abstract applyLinearTransform(f:(_:Point)=>Point):Path|singularity;

    /**
     * Split if the path strays too far from the straight
     * segment from start() to end().  "Too far" is
     * vaguely expressed by 'tolerance', where a value of 1
     * means roughly the distance from start() to end().
     * 
     * Note, we need this in front of mos.normalizePathEndpoints
     * to prevent a nearly-closed loop from collapsing to a singularity.
     * 
     * otherwise return [this].
     * @param tolerance
     * @param recursionLimit
     */
    splitErrant(tolerance: number, recursionLimit: number):Path[] {
        
        if (recursionLimit<=0) {
            console.warn("Hit recursion limit on splitErrant");
            return [this];
        }
        if (!this.isErrant(tolerance)) return [this];
        let split: [Piece, Piece] | singularity = this.splitNatural();
        if (split===singularity) return [this];
        let segs =split as [Piece, Piece];
        let [{path:a1},{path:b1}] = segs;
        // console.log("Splitting errant "+this.hString()+"\nleft: "+a1.hString()+"\nright: "+b1.hString());
        let a2 = a1.splitErrant(tolerance, recursionLimit - 1);
        let b2 = b1.splitErrant(tolerance, recursionLimit - 1);
        return  [...a2, ...b2];
    }

    /** Move and rotate rigidly. The result's start point must be exactly newStart without rounding errors.*/
    moveRigidToStart(newStart:Point, newStartTangent:Angle):Path|singularity {
        let a = this.startTangent();
        let da = newStartTangent.minus(a);
        let e = this.end();
        let s = this.start();
        if (s.equals(newStart) && Math.abs(da.radians()) < 0.00001)  return this;
        let f:(_:Point)=>Point;
        // If it's a closed loop, we need to do a rotation without translation
        if (e.dist(s) < PRECISION_EPSILON) {
            f = (p:Point) => {
                if (p.equals(s)) return newStart; // To enforce no rounding errors on the endpoint.
                let v = p.minus(s);
                v = v.divideComplex(a.vector());
                v = v.timesComplex(newStartTangent.vector());
                return newStart.plus(v);
            }    
        } else {
            let newDirection = e.minus(s).rotate(da);
            let newEnd = newStart.plus(newDirection);
            f = anglePreservingTransformation(s,newStart, e,newEnd);
        }
        let result = this.applyLinearTransform(f);
        if (result===singularity) return singularity;
        if (!result.start().equals(newStart) ) // exact
            console.log ("moveRigidToStart:start/end assertion failed")
        // if (!result.end().equals(newEnd) ) // exact
        //     console.log ("moveRigidToStart:start/end assertion failed")
        return result;
    }
    moveRigidToEnd(newEnd:Point, newEndTangent:Angle):Path|singularity {
        let p = this.reverse().moveRigidToStart(newEnd, newEndTangent.reverse());
        return p===singularity ? singularity : p.reverse();
    }
    
    /** Create a spline transitioning from A to B as t goes from 0 to 1.
     *  The start of the spline will be exactly A.start() without rounding errors,
     *  and the end B.end(). */
    static spline(A:Path,B:Path):Path {
        let alin = A.linearize();
        let blin = B.linearize();
        let a = alin.segs.slice();
        let b = blin.segs.slice();
        // combine the corner points of both
        let ts=[0];
        let t=0;
        while(a.length&&b.length) {
            let da = a[0].t2 - a[0].t1;
            let db = b[0].t2 - b[0].t1;
            // If they both have only one segment left, then they must be the same...
            if (a.length === 1 && b.length === 1 || Math.abs(da-db)<0.0000001) {
                ts.push(t+da);
                t+=da;
                a.shift();
                b.shift();
            } else {
                if (da > db) [a, b, da, db]=[b, a, db, da];
                ts.push(t+da);
                t+=da;
                a.shift();
                let {path,t1,t2} = b.shift();
                t1 = t1+da;
                if (t1<t2) b.unshift({path,t1,t2});
            }
        }
        let Chain = require('./Chain').Chain;
        let Segment = require('./Segment').Segment;
        // If any remain in a or b, they can only be rounding errors; ignore them.
        let newPoints=[A.start()]
        for (let i=1;i<ts.length-1;i++) {
            let t=ts[i];
            let a = alin.pointAt(t);
            let b = blin.pointAt(t);
            // interpolates 0 to 1 with zero slope at 0,1;  y=3x^2-2x^3
            // i.e. x(t) = (1-u(t))a(t) + u(t)b(b), where  u = 3t^2-2t^3 = t^2(3-2t)
            // i.e. x(t) = a(t) + (b(t)-a(t))u(t)
            // x'(t) = a'(t) + u(t)(b'(t)-a'(t)) + (b(t)-a(t))u'(t), where u'(t)=6t-6t^2 = 6t(1-t)
            // x'    = a' +  (t^2(3-2t)) (b'-a') + (b-a)(6t(1-t))
            let u = t * t * (3 - 2 * t);
            let x = a.to(b, u);
            newPoints.push(x);
        }
        newPoints.push(B.end());
        let newSegs:Piece[]=[];
        for (let i=1;i<ts.length;i++) {
            newSegs.push({path:Segment.of(newPoints[i - 1], newPoints[i]),
                             t1:ts[i - 1], t2:ts[i]});
        }
        return Chain.of(newSegs);
    }

    /**
     * Bend and scale the curve as indicated.
     * The new curve's endpoints must be exactly newStart,newEnd
     * with no rounding errors.
     */
    bend(newStart: NPoint, newStartTangent: Angle,
         newEndTangent: Angle, newEnd: NPoint,
         stretch: DistortionOption = DISTORTION_STRETCHY)
        : Path|singularity
    {
        let result=this._bend(newStart,newStartTangent,newEndTangent,newEnd,stretch);
        if (result===singularity) return singularity
        if (!result.start().equals(newStart)) console.log("bend contract failed")
        if (!result.end().equals(newEnd))     console.log("bend contract failed")
        return result;
    }
    
    abstract _bend(newStart: NPoint, newStartTangent: Angle,
         newEndTangent: Angle, newEnd: NPoint,
         stretch: DistortionOption): Path|singularity;
    // This was a generic implementation that was inefficient
    // and can't handle some singular cases:
    // {
    //     let lin = this.linearize();
    //     let A = lin.moveRigidToStart(newStart,newStartTangent);
    //     let B = lin.moveRigidToEnd(newEnd,newEndTangent);
    //     let C = Path.spline(A,B);
    //     if (!C.start().equals(newStart) || !C.end().equals(newEnd))
    //         console.log ("bend: start/end assertion failed")
    //     return C;
    // }

    autoBend(newStart: NPoint, newEnd: NPoint,
             looseStart: boolean, looseEnd: boolean,
             dragOptions: DragOptions):Path|singularity
    {
        let path=this;

        let result;

        // This is wrong; it might be a loop!
        // if (path.start().equals(path.end())) {
        //     return path.applyLinearTransform(translation(path.start(),newStart));
        // } 
    
        let [o1,o2] = [dragOptions.distortion, dragOptions.endTangents];
        if (o2===END_TANGENTS_FIXED) {
            
            // Use the bend algorithm, with or without initial scaling
            let nst = path.startTangent();
            let net = path.endTangent();
            result = path.bend(newStart, nst, net, newEnd, o1);

        } else if (o2===END_TANGENTS_FREE) {
            
            let f = (o1===DISTORTION_STRETCHY)
                    ?  stretchTransformation(path.start(),newStart,path.end(),newEnd)
                    : anglePreservingTransformation(path.start(),newStart,path.end(),newEnd)
            result = path.applyLinearTransform(f);

        } else {
            // use the bend algorithm, with loose end behavior, with option o1
            let nst = path.startTangent();
            let net = path.endTangent();
            let LOOSE_END_WAG = 0.7;
            if (looseStart && looseEnd) {
                let PQ = path.end().minus(path.start());
                let RS = newEnd.minus(newStart);
                let dth = new Angle(RS).minus(new Angle(PQ));
                result = path.bend(newStart, nst.plus(dth), net.plus(dth), newEnd,o1);
            } else if (looseEnd) {
                // Don't preserve the original start tangent, let it flail
                let PQ = path.end().minus(path.start());
                let PR = newEnd.minus(newStart);
                let rot = new Angle(PR).minus(new Angle(PQ));
                let th = net.reverse();//new Angle(PQ).reverse();
                th = th.plus(rot.times(1+LOOSE_END_WAG));
                result = path.bend(newStart, nst, th.reverse(), newEnd,o1);
            } else if (looseStart) {
                let PQ = path.start().minus(path.end());
                let PR = newStart.minus(newEnd);
                let rot = new Angle(PR).minus(new Angle(PQ));
                let th = nst;//new Angle(PQ).reverse();
                th = th.plus(rot.times(1+LOOSE_END_WAG));
                result = path.bend(newStart, th, net, newEnd,o1);
            } else {
                result = path.bend(newStart, nst, net, newEnd,o1);
            }
        } 
        if (!result) throw new Error("unhandled case");
        if (result!==singularity && !result.start().equals(newStart)) console.log('contract failed')
        if (result!==singularity && !result.end().equals(newEnd)) console.log('contract failed')
        return result;
    }
    
    length():number {
        return this.fragmentize().segs
            .map(seg=>seg.path.length())
            .reduce((a, b)=> a + b);
    }

    bounds():Bounds {
        let r = this.$bounds || this.$reversed?.$bounds;
        if (!r) {
            r = this._bounds();
            this.$bounds = r;
        }
        return r;
    }

    abstract _bounds():Bounds;


    // Same path in the backwards direction
    reverse():Path {
        let r = this.$reversed;
        if (!r) {
            r = this._reverse();
            r.$reversed = this;
            this.$reversed = r;
        }
        return r;
    }

    abstract _reverse():Path;

    /**
     * Replace with a Path whose parameterization is linear with regard to distance
     * along the curve.  For non-segments, this is done by fragmentizing.
     */
    linearize():Chain {
        let v = this.$linearize
        if (!v && this.$reversed?.$linearize) {
            v = this.$reversed.$linearize.reverse();
        }
        if (!v) {
            v =  this._linearize();
            this.$linearize=v;
        }
        return v;
    }

    _linearize():Chain {
        // @ts-ignore
        // console.log("linearized 1to "+this?.segs?.length+" segs")
        let segs: Piece[] = this.fragmentize().segs;
        // Make each one the right length
        segs = segs.map((seg) => ({path: seg.path, t1: 0, t2: seg.path.start().dist(seg.path.end())}));
        // this will stitch them together
        let Chain = require('./Chain').Chain;
        return Chain.of(segs);
        // console.log("linearized to "+v.segs.length+" segs")
        // v.$fragmentize = v.$linearize = v;
    }

    smooth():Path {
        let Chain = require('./Chain').Chain;
        let Segment = require('./Segment').Segment;
        let scale=20;
        let threshold=1;
        if (this.bounds().size()<scale) return this._smooth(threshold);
        let n = this.splitNatural();
        if (n===singularity) return this;
        return Chain.of(n.map(piece => piece.path.smooth())
                            .map(p => ({path: p, t1: 0, t2: 1})));
    }
    _smooth(threshold:number):Path {
        let Chain = require('./Chain').Chain;
        let Segment = require('./Segment').Segment;
        let f: Path = this.fragmentize();
        if (f.isSegment()) return this;
        // It's a chain.
        if (this.start().equals(this.end())) {
            let n = this.splitNatural();
            if (n===singularity) return this;
            else {
                return Chain.of(n.map(piece => piece.path.smooth())
                                    .map(p => ({path: p, t1: 0, t2: 1})));
            } 
        }
        
        let segs: Piece[] = (f as Chain).segs;
        let points:Point[] = segs.map(seg => seg.path.end());
        points.unshift(segs[0].path.start());
        let RamerDouglasPeuckerSmoothing = require('./RDPSmoothing').RamerDouglasPeuckerSmoothing;
        let smoothToCurves = require('./RDPSmoothing').smoothToCurves;
        let a=points.length;
        let ps: Point[] = RamerDouglasPeuckerSmoothing(points, threshold);
        let b=ps.length;
        console.log(" smoothing " + a + " / " + b);
        let v: Path[] | singularity = smoothToCurves(ps);
        if (v===singularity) {
            console.log("singularity");
            return this; 
        } else {
            let result = Chain.of(v.map(p => ({path: p, t1: 0, t2: 1})))
            result = result.bend(this.start(), this.startTangent(),
                                 this.endTangent(), this.end(),
                                 "DISTORTION_ORTHORGONAL");
            return result;
        }
        
    }

    /**
     * Fragmentize and then subdivide more until all the segments are of length
     * less than PRECISION_PATH/2
     */
    totallyShatter():Path {
        let Chain = require('./Chain').Chain;
        let Segment = require('./Segment').Segment;
        let v:Path = this.$totallyShatter;
        if (!v && this.$reversed?.$totallyShatter) {
            v = this.$reversed.$totallyShatter.reverse();
        }
        if (!v) {
            v = this.smooth();
            this.$totallyShatter=v;
        }
        return v;
    }

    /**
     * Recursively splitNatural() until the curve is entirely approximated by Segments
     * within a precision of PRECISION_PATH.  All of the intermediate points
     * will be exactly on the curve.
     * The resulting Chain will be  properly parameterized from 0 to 1 such that
     * frag(t) approximates path(t) (not necessarily linear with regard to distance
     * along the curve)
     */
    // xxx recursionLimit was addeda
    // Todo, recursionLimit isn't factored into the cache; it needs to be hardcoded
    // todo, this actually returns Path
    fragmentize(recursionLimit:number=1000):Chain {
        let v = this.$fragmentize;
        if (!v && this.$reversed?.$fragmentize) {
            v = this.$reversed.$fragmentize.reverse();
        }
        if (!v) {
            v = this._fragmentize(recursionLimit);
            this.$fragmentize=v;
        }
        return v;
    }
    
    // todo, this actually returns Path
    _fragmentize(recursionLimit:number=1000):Chain {
        let Chain = require('./Chain').Chain;
        if (this.isSegment()) return Chain.of([{path:this,t1:0,t2:1}]);
        // Here's where one of the pieces might be reduced to a Segment
        let split=this.splitNatural();
        if (split === singularity) {
            return Chain.of([{path:this,t1:0,t2:1}]);
        }
        let [cs1, cs2]=split;
        let recurse = (cs:Piece, recursionLimit:number):Piece[]=>{
            if (cs.path.isSegment()) return [cs];
            if (recursionLimit--<0) {
                return [cs];
            }
            let frags = cs.path.fragmentize(recursionLimit).segs;
            // map frags' parameters 0-1 to parent parameters cs.t1-cs.t2 
            return  frags.map(f=>({path:f.path, t1:cs.t1 + (cs.t2-cs.t1)*f.t1,
                                                t2:cs.t1 + (cs.t2-cs.t1)*f.t2}));
        };
        let c= Chain.of(recurse(cs1,recursionLimit).concat(recurse(cs2,recursionLimit)));
        // console.log("fragmentized to "+c.segs.length+" segs");
        // c.$fragmentize=c;
        return c;
    }
    
    isSegment():boolean {
        return false;
    }
    
    isSingularity():singularity|false {
        return false;
    }
    
    /**
     * Split exactly at some "natural" place (depending on type of path)
     * suitable for the purpose of binary search or breaking apart
     * into less complex pieces.  The resulting parameterization
     * must be of the form [{... t1:0, t2:t}, {... t1:t, t2:1}].
     * The resulting pieces may be approximated by segments within PRECISION_PATH.
     * Returns SINGULARITY if the original segment was too small to be split.
     */
    splitNatural():[Piece,Piece]|singularity {
        let v = this.$splitNatural;
        if (!v) {
            v = this._splitNatural();
            this.$splitNatural = v;
        }
        return v;
    }
    
    _splitNatural():[Piece,Piece]|singularity {
        let [a,b] = this.split2(0.5);
        if (a === singularity || b === singularity) return singularity;
        return [{path:a,t1:0,t2:0.5},{path:b,t1:0.5,t2:1}];
    }

    /**
     * Return it as a list of ChainSegment, even if it isn't a Chaina
     */
    unchainRecursive():Piece[] {
        return [{path:this,t1:0,t2:1}]
    }

    // for debugging chains
    innerPointsOfNaturalInterest():Point[] {
        return []
    }
    
    /**
     * Heuristic.  True if the path strays by a factor of tolerance
     * away from the straight line between start() and end()
     */
    isErrant(tolerance:number):boolean {
        if (this.start().equals(this.end())) return this.bounds().w>0 || this.bounds().h>0;
        let f: (xx: P) => P = anglePreservingTransformation(this.start(), Point.of(0,0),
                                                            this.end(), Point.of(1,0));
        let norm = this.applyLinearTransform((p) => Point.forP(f(p)));
        if (norm === singularity) {
            return false;
        }
        let bb=norm.bounds();
        return  bb.y < -tolerance || bb.my > tolerance
    }

    abstract startTangent():Angle;
    abstract endTangent():Angle;
    
    equals(other:Path):boolean {
        return this===other 
               || this.start().equals(other.start()) 
               && this.svgPath()===other.svgPath();    
    }

    /**
     * If the path goes through or near the given point (within the given radius)
     * split it exactly at a point near the given point within radius, else return null
     */
    // xx
    splitForPoint3(p: Point, radius: number): [Path|singularity, Path|singularity]|null {
        // todo is this good?
        // return this.genericSplitForPoint(p, precision, finePrecision, endpointPrecision);

        let fp = this._findPoint2(p, radius);
        if (!fp) return null;
        if (fp.t<=0) return [singularity,this]; // maybe redundant
        if (fp.t>=1) return [this,singularity];
        return this.split2(fp.t);

    }

    /**
     * 
     * Find the parameter at which the curve is nearest the given point, within the given radius, by fragmentizing
     * and then obtaining an exact solution.  This could be improved by using recursion on the fragmentized version..
     */
    _findPoint2(p:Point, maxRadius:number):SplitWithPrecision|null {
        if (!this.bounds().intersectsCircle(p, maxRadius))
            return null;
        let sps = this.fragmentize().segs.map(cs=>{
            let seg = cs.path; 
            if (!seg.isSegment()) throw new Error('assertion failed, not a segment');
            let pt= closestPoint(seg.start(), seg.end(), p);
            let t = cs.t1 + (cs.t2-cs.t1)*pt.t;
            return {t:t, precision:p.dist(pt)} as SplitWithPrecision;
        });
        let result = sps.reduce((sp1,sp2)=>sp1.precision < sp2.precision ? sp1 : sp2);
        return result.precision < maxRadius ? result : null;
    }
    
    
}
export interface NearIntersection {
    p1:P;  // point on first path
    p2:P;  // point on second path
    dist:number; // distance between them, exactly zero if they are the same
}
function reduceNearIntersections(precision:number, fs:(()=>NearIntersection)[]):NearIntersection|null {
    let result:NearIntersection=null;
    for (let f of fs) {
        if (result === null) {
            result = f();
            if (result && result.dist>precision) result=null; 
            if (result && !result.dist) return result;
        } else {
            let r2 = f();
            if (r2 && r2.dist>precision) r2=null;
            if (r2) {
                if (!r2.dist) return r2;
                if (r2.dist<result.dist) result=r2;
            }
        }
    }
    return result;
}

/** Find intersection of paths made of segments.  This is an unused alterenative to findIntersection.
 * 
 * NEEDS REVIEW FOR HANDLINLG OF PRECISION IF WE WANT TO USE IT.
 */
export function findFragsIntersection(p1:Path, p2:Path, excluded:Point[],prec:number, recursion:number):NearIntersection|null {
    recursion++;
    let b1 = p1.bounds();
    let b2 = p2.bounds();
    // if they don't even overlap at all, stop.   Note this is necessary for efficiency
    // but prevents detection of close misses and narrow angles.
    if (!b1.intersectsBounds(b2)) return null;
    // If we're searching too close to the endpoints, stop
    if (excluded.some(e=>(b1.isInsideCircle(e,prec) || b2.isInsideCircle(e,prec)))) {
        return null;
    }
    // We know the bounds are overlapping.
    // If we've narrowed it down to segments...
    let f1 = p1.fragmentize().segs;
    let f2 = p2.fragmentize().segs;
    if (f1.length===1 && f2.length===1) {
        // find exact intersection with segment
        let seg1 = f1[0].path;
        let seg2 = f2[0].path;
        let i = intersectionOfLineSegments(seg1.start(), seg1.end(), seg2.start(), seg2.end());
        if (i!=null) return {p1:i,p2:i,dist:0};
        // No exact intersection, or the segments are parallel. 
        // Either way, a nearest approach can always be found at one of the endpoints.
        return reduceNearIntersections(prec, [() => {
                                            let x = seg2.start();
                                            let p = closestPoint(seg1.start(), seg1.end(), x);
                                            return p && {p1: p, p2: x, dist: dist(p, x)}
                                        },
                                        () => {
                                            let x = seg2.end();
                                            let p = closestPoint(seg1.start(), seg1.end(), x);
                                            return p && {p1: p, p2: x, dist: dist(p, x)}
                                        },
                                        () => {
                                            let x = seg1.start();
                                            let p = closestPoint(seg2.start(), seg2.end(), x);
                                            return p && {p1: x, p2: p, dist: dist(p, x)}
                                        },
                                        () => {
                                            let x = seg1.end();
                                            let p = closestPoint(seg2.start(), seg2.end(), x);
                                            return p && {p1: x, p2: p, dist: dist(p, x)}
                                        }]);
    } else {
        // let #1 be the longer one
        if (f1.length<f2.length) {
            [p1,p2,b1,b2,f1,f2] = [p2,p1,b2,b1,f2,f1];
        }
        // Split the longer one. We know the longer one has at least
        // two segments in its frag, so it wasn't a segment and we can keep going.
        let splitNatural = p1.splitNatural();
        if (splitNatural===singularity)// I think this can't happen 
            return null; 
        let [q,r] = splitNatural;
        // Alternate how we recurse on the split to avoid favoring ends of paths
        if (recursion%4<2) [q,r] = [r,q];
        return reduceNearIntersections(prec, [()=> findFragsIntersection(q.path, p2, excluded, prec, recursion),
                                        ()=> findFragsIntersection(r.path, p2, excluded, prec, recursion)]);
    }

}

// Todo, we didn't actually make the transition from the segment approximation
// to the actual t-value and actual point.  No-no.
export function findPathInnerIntersection2(p1:Path, p2:Path, precision:number):Point {
    let ni = findFragsIntersection(p1,p2,[p1.start(),p1.end(),p2.start(),p2.end()],precision,0);
    return ni && ni.dist<=precision && Point.forP(ni.p1);
}

/**
 * Find an intersection point that doesn't lie on the endpoints.
 * We don't need to find near-intersection points (??)
 * This finds a point that lies on at least one of the curves and is within
 * precision of the other curve.
 */
export function findPathInnerIntersection(p1:Path, p2:Path, precision:number):Point {
    let RECURSION_LIMIT=40;
    
    return _findPathInnerIntersections(p1,p2,
                                       p1.start(),p1.end(),p2.start(),p2.end(),
                                       precision,RECURSION_LIMIT);
}
// xxx todo, it would be better if this returned the parameterizations.
export function _findPathInnerIntersections(p1: Path, p2: Path,
                                     s1:Point,e1:Point,s2:Point,e2:Point, /// original endpoints
                                     precision: number, recursionLimit: number):Point 
{
    if (recursionLimit--<0) {
        console.warn("Hit recursion limit in _findPathInnerIntersections")
        return null;
    }
    let b1 = p1.bounds();
    let b2 = p2.bounds();
    
    // if they don't even overlap at all, stop.   Note this is necessary for efficiency
    // but prevents detection of close misses and narrow angles.
    if (!b1.intersectsBounds(b2)) return null;
    // if we try to make it more fuzzy, performance tanks.
    // if (!b1.intersectsBoundsFuzzy(b2,PRECISION)) return null;
    
    // If we're searching too close to the endpoints, stop
    if (b1.isInsideCircle(s1,precision)) return null;
    if (b1.isInsideCircle(e1,precision)) return null;
    if (b2.isInsideCircle(s2,precision)) return null;
    if (b2.isInsideCircle(e2,precision)) return null;


    // we know they're overlapping.  If they're small enough
    // we're done.
    let finePrecision = precision/16;  // somewhat arbitrary
    // let u = b1.union(b2);
    // if (Math.max(u.w,u.h)<finePrecision) {
    //     return p1.start();
    // } 
    if (b1.extentWith(b2)<finePrecision) {
        return p1.start();
    }
    
    // else keep chopping; favor the one with the smallest bounding box,
    // as that seems to be more efficient for some reason????
    let larger1:boolean = b1.w+b1.h>b2.w+b2.h;
    if (larger1 != (recursionLimit%5===0)) {
        // Assume p2 is larger. Swap so that p1 is larger.
        [p1,p2,s1,s2,e1,e2,b1,b2] = [p2,p1,s2,s1,e2,e1,b2,b1];
    }
    // Assume p1 is larger. try to split p2 first.
    let splitNatural = p2.splitNatural();
    if (splitNatural===singularity) {
        //xxx previously, we continued the recursion even though it was a singularith.a
        // return null;
        
        // Didn't work out.  Choose the other one and try again
        [p1,p2,s1,s2,e1,e2,b1,b2] = [p2,p1,s2,s1,e2,e1,b2,b1];
        splitNatural = p2.splitNatural();
        if (splitNatural===singularity) {
            // They were both singularities upon splitting. 
            return null;
        }
    } 
    let [{path:c},{path:d}]= splitNatural;
    // alternate the order so we're not favoring points close to endpoints
    if (recursionLimit%4<2) {
        return    _findPathInnerIntersections(p1,c,s1,e1,s2,e2,precision,recursionLimit) ||
                  _findPathInnerIntersections(p1,d,s1,e1,s2,e2,precision,recursionLimit);
    } else {
        return _findPathInnerIntersections(p1, d, s1, e1, s2, e2, precision, recursionLimit) || 
               _findPathInnerIntersections(p1, c, s1, e1, s2, e2, precision, recursionLimit);
    }
     

}

export interface SplitWithPrecision {
    t:number;  // splitting parameter
    precision:number; /// precision that was achieved
}

