import {StateContext} from "./App";
import React, {useEffect, useState} from 'react';
import {Mos} from "./Mos";
import {P} from "../Bounds";
import {Point} from "../Math/Point";
import {
    DISTORTION_ORTHORGONAL,
    DISTORTION_STRETCHY, dragOptionsWithModifierKeys,
    END_TANGENTS_FIXED,
    END_TANGENTS_FLOPPY,
    END_TANGENTS_FREE
} from "../Math/DragOptions";


interface Drag { 
    from:P,
    to:P,
    poid:string,
    orig:Mos, 
    shiftKey:boolean, 
    controlKey:boolean;
};

export const PoisInteractions = (props:{container: React.RefObject<HTMLDivElement> }) => {
    
    const [ drag, setDrag] = useState<Drag|null>(null);
    const { state, setState } = React.useContext(StateContext);

    function keyup(event:KeyboardEvent) {
        if (!drag) return;
        
        doDrag(null, event.shiftKey, event.ctrlKey)
    }
    function keydown(event:KeyboardEvent) {
        if (!drag) return;
        doDrag(null, event.shiftKey, event.ctrlKey)
    }
    function mousedown(event: MouseEvent) {
        if (event.button!==0) return;
        // @ts-ignore
        let id = event.target.getAttribute("poid");
        if (!id) return;
        // can't use this because behaves differently on safari
        // let bbox = event.target.getBBox();
        let to = state.coo.translateContainerToSvg(event.clientX, event.clientY);
        let from = to;
        let drag2 = {
            from:from,
            to:to, 
            poid:id, orig:state.mos,
            shiftKey: event.shiftKey,
            controlKey: event.ctrlKey,
        };
        setDrag(drag2);
        let u = state.undo.slice();
        u.push(state.mos);
        setState({...state,undo:u})
    }

// version with snapping
    function mousemove(event: MouseEvent) {
        if (event.button!==0) return;
        if (!drag) return;
        let to = state.coo.translateContainerToSvg(event.clientX, event.clientY);
        doDrag(to, event.shiftKey, event.ctrlKey);
    }


    function doDrag(svgCoords:P|null,  shiftKey: boolean, controlKey: boolean) {
        let drag2 = {...drag, to: svgCoords||drag.to, 
                      shiftKey: shiftKey, controlKey: controlKey};
        setDrag(drag2);
        let mos = drag2.orig.clone();
        let poi = mos.pois[drag2.poid]
        if (poi) {
            let delta = Point.forP(drag2.to).minus(drag2.from);
            let dest = poi.p.plus(delta);

            // Remove the poi and its adjacent paths from the clone
            // so we can decide where else to snap to
            let snap = state.snapping * Math.max(1, state.coo.pixelRatio());
            // don't snap so strongly if it's not a loose endpoint
            if (poi.star.length > 1) snap = snap / 3;
            // Do the snap
            if (snap>0) {
                let mos2 = mos.clone();
                let poisToPrune: string[] = []
                for (const {pathID} of poi.star) {
                    let toRemove = mos2.paths[pathID];
                    mos2._remove(toRemove)
                    // poisToPrune.push(toRemove.end().id())
                    // poisToPrune.push(toRemove.start().id())
                }
                mos2.prunePOI(poi.p.id());
                for (let e of poisToPrune) {
                    mos2.prunePOI(e);
                }
                dest = mos2.getSnapTo(dest, snap).p;
            }

            let d = dragOptionsWithModifierKeys(state.dragOptions, shiftKey, controlKey);

            mos = mos.clone();
            mos.movePOI(poi, dest, d)
            setState({...state, mos: mos});
        } else {
        }
    }

    function mouseleave(event: MouseEvent) {
        if (event.button!==0) return;
        if (!drag) return;
        // cancel it
        let u = state.undo.slice();
        let m = u.length ? u.pop() : state.mos;
        setState({...state, mos:m, undo:u})
        setDrag(null);
    }
    function mouseup(event: MouseEvent) {
        if (event.button!==0) return;
        if (!drag) return;
        
        // hammer it?  after we fix the makeChains efficiency problem.
        // let mos = new Mos();
        // let allUnchained = Object.values(state.mos.paths)
        //     .flatMap(path=>path.unchainRecursive())
        //     .map(cs=>cs.path);
        // mos.accept(allUnchained, true);
        // setState({...state,mos:mos});

        setDrag(null);
    }
    
    useEffect(() => {
        let p: HTMLDivElement = props.container.current;
        p.addEventListener("mouseup", mouseup);
        p.addEventListener("mouseleave", mouseleave);
        p.addEventListener("mousemove", mousemove);
        p.addEventListener("mousedown", mousedown);
        document.addEventListener("keyup", keyup);
        document.addEventListener("keydown", keydown);
        return () => {
            p.removeEventListener("mouseup", mouseup);
            p.removeEventListener("mouseleave", mouseleave);
            p.removeEventListener("mousemove", mousemove);
            p.removeEventListener("mousedown", mousedown);
            document.removeEventListener("keyup", keyup);
            document.removeEventListener("keydown", keydown);
        };
    }, [state,drag, mousedown, mouseleave, mousemove, mouseup, keyup, keydown, props.container]);
    
    // Get all of the paths in the relevant star; we'll be modifying these
    if (!(drag && drag.poid && state.mos.pois[drag.poid])) {
        return null;
    }

    
    return <></>;
    
};
