import {P} from "../Bounds";
import {Point} from "./Point";

const FRONT_SVG = "displaySvg";
const BACK_SVG = "origSvg";
// both should have the same size
const CONTAINER_FRONT="svgParent";

// For checking equality
export const PRECISION_EPSILON = 10**-14 // note Number.EPSILON is around 2*10^-16
// The minimum distance between any two path endpoints
export const PRECISION_PIXEL=1.0;
// The precision to use for numerical estimation of intersections and nearest-points
export const PRECISION_NUMERICAL = PRECISION_PIXEL / 1024;
// To define approximate equivalence of paths
export const PRECISION_PATH = PRECISION_PIXEL / 16;

export var OriginalSvgDocument:{svg:SVGSVGElement|null} = {svg:null };

export function equalsfp(a:number,b:number) {
    return Math.abs(a-b)<PRECISION_EPSILON;
}

function container(): HTMLElement { 
    let e =  document.getElementById(CONTAINER_FRONT);
    if (!e) throw new Error("unexpected");
    return e;
}

function svgFront():SVGSVGElement {
    return document.getElementById(FRONT_SVG) as unknown as SVGSVGElement;
}
function svgBack():SVGSVGElement {
    return document.getElementById(BACK_SVG) as unknown as SVGSVGElement;
}


/////////// math ////////////

function minus(p:P, q:P):P {
    return {x:p.x-q.x, y:p.y-q.y};
}
function dot(p:P, q:P):number {
    return p.x*q.x+ p.y*q.y;
}
export function dist(p:P, q:P):number {
    return Math.sqrt(dot(p,q));
}
function plus(p:P, q:P):P {
    return {x:p.x+q.x, y:p.y+q.y};
}
function atan(p:P, q:P):number {
    return Math.atan2(q.y-p.y,q.x-p.x);
}
function rotate(p:P, theta:number):P {
    return {x:p.x*Math.cos(theta) - p.y*Math.sin(theta),
            y:p.x*Math.sin(theta) + p.y*Math.cos(theta)};
}
function scalarMultiply(a:number,p:P) {
    return {x:a*p.x, y:a*p.y};
}

export function translation(p:P,p2:P):(_:P)=>Point {
    let dx=p2.x-p.x;
    let dy=p2.y-p.y;
    return (input:P)=>(Point.of(input.x+dx,input.y+dy));
}

// The linear transformation that preserves angles
// and maps p to p2, q to q2.  no rounding errors on those particular points.
export function anglePreservingTransformation(p:Point, p2:Point, q:Point, q2:Point):(_:P)=>Point {
    
    // To enforce no rounding errors on the endpoints.
    let sanitize = (f:(_:P)=>Point) => (x:P) => (p.equals(x) ? p2 : q.equals(x) ? q2 : f(x));

    let d = minus(q,p);
    if (dot(d,d)<.000000001) {
        console.log("anglePreservingTransformation: element too small, not applying rotation: "
                    +Point.forP(p).hString()+"-"+Point.forP(q).hString());
        return sanitize((input: P) => (Point.of(input.x + p2.x - p.x, input.y + p2.y - p.y)));
    }
    // Todo, rewrite in terms of Points and Vectors
    let d2 = minus(q2,p2);
    let r = Math.sqrt(dot(d2,d2)/dot(d,d));
    let t = atan(p,q);
    let t2 = atan(p2,q2);
    let dt = t2-t;
    return sanitize((input:P):Point=>{
        let a = input;
        a = minus(a,p);
        a = scalarMultiply(r,a);
        a = rotate(a, dt);
        a = plus(a,p2);
        return Point.forP(a);
    });
}

export function stretchTransformation(p:Point, p2:Point, q:Point, q2:Point):(_:P)=>Point {

    // To enforce no rounding errors on the endpoints.
    let sanitize = (f:(_:P)=>Point) => (x:P) => (p.equals(x) ? p2 : q.equals(x) ? q2 : f(x));

    let pq = minus(q,p);
    if (dot(pq,pq)<.000000001) {
        console.log("stretchTransformation: element too small, not applying rotation: "
                    +Point.forP(p).hString()+"-"+Point.forP(q).hString());
        return sanitize((input: P) => (Point.of(input.x + p2.x - p.x, input.y + p2.y - p.y)));
    }
    // Todo, rewrite in terms of Points and Vectors
    let pq2 = minus(q2,p2);  // final distance
    let r = Math.sqrt(dot(pq2,pq2)/dot(pq,pq));  // ratio of distances
    let t = atan(p,q);   // original angle
    let t2 = atan(p2,q2);  // final angle
    let dt = t2-t;      // rotation
    return sanitize((input:P):Point=>{
        let a = input;
        a = minus(a,p);  // translate to origin
        a = rotate(a, -t);
        a = Point.of(a.x*r, a.y) // not scalarMultiply(r,a);
        a = rotate(a, t2);
        a = plus(a,p2);
        return Point.forP(a);
    });
}


// Find the closest point to x on the line segment a->b, and the parameter
export function closestPoint(a:P, b:P, x:P): P & {t:number} {
    let ab = minus(b,a);
    let len2 = (dot(ab,ab));
    let ax = minus(x,a);
    let t = dot(ab,ax) / len2;
    t = Math.max(0, Math.min(t, 1));
    return {x: a.x + ab.x * t, y: a.y + ab.y * t, t:t};
}


//https://chat.openai.com/chat/2513291f-cbef-4472-a353-7b52b0d4cf77
// Returns the point of intersection if the segments aren't parallel
export function intersectionOfLineSegments(a1: P, a2: P, b1: P, b2: P): P | null {
    function dotProduct(p: P, q: P): number {
        return p.x * q.x + p.y * q.y;
    }

    function subtract(p: P, q: P): P {
        return { x: p.x - q.x, y: p.y - q.y };
    }
    const p = subtract(a2, a1);
    const q = subtract(b2, b1);
    const r = subtract(a1, b1);
    const pq = dotProduct(p, q);
    const pp = dotProduct(p, p);
    const qq = dotProduct(q, q);
    const rp = dotProduct(r, p);
    const rq = dotProduct(r, q);
    const denominator = pq * pq - pp * qq;

    // Check if the two line segments are parallel
    if (denominator === 0) {
        return null;
    }

    const t = (rp * qq - rq * pq) / denominator;
    const u = (rp * pq - rq * pp) / denominator;

    // Check if the intersection point is within both line segments
    if (t >= 0 && t <= 1 && u >= 0 && u <= 1) {
        return { x: a1.x + t * p.x, y: a1.y + t * p.y };
    } else {
        return null;
    }
}


export function shapeColor(x:number):string {
    // x = Math.max(0,Math.min(2,x))
    x = Math.max(0,x);
    x = 2*x/(x+1);
    
    let r, g, b;
    if(x <= 1) {
        x = 1-x;
        r = 128*(1-x) + 127 * x;
        g = 128*(1-x) + 0 * x;
        b = 128*(1-x) + 0 * x;
    } else {
        x = x-1;
        r = 128*(1-x) + 0 * x;
        g = 128*(1-x) + 127 * x;
        b = 128*(1-x) + 0 * x;
    }
    return `rgba(${Math.round(r)},${Math.round(g)},${Math.round(b)},0.6)`;
}

