import {Bounds, P} from "../Bounds";
import {closestPoint} from "./const";
import {Path, singularity, SplitWithPrecision} from "./Path";
import {Angle, Point} from "./Point";
import {Chain} from "./Chain";
import {NPoint} from "../Reacts/Mos";
import {DISTORTION_STRETCHY, DistortionOption} from "./DragOptions";

export class Segment extends Path {

    readonly p1: Point;
    readonly p2: Point;
    private _svg: string;
    private _hString: string;

    /** returns a segment unless p1.equalsfp(p2) */
    static of(p1:Point, p2:Point):Segment|singularity {
        if (p1.equalsfp(p2)) return singularity;
        return new Segment(p1,p2);
    }
    /** returns a segment without checking for singularity */
    static ofNonsingular(p1:Point, p2:Point):Segment {
        return new Segment(p1,p2);
    };
    
    static dividedSegment(p1:Point, p2:Point, maxLength:number):Segment[] {
        
        let s = new Segment(p1,p2);
        let d = s.length() / maxLength;
        if (d<=1) return [s];
        
        // Todo divide the line segment (p1,p2)
        // into d equal sized segments
        const n = Math.ceil(d);
        const deltaX = (p2.x - p1.x) / n;
        const deltaY = (p2.y - p1.y) / n;
        const segments: Segment[] = [];

        for (let i = 0; i < n; i++) {
            const start = Point.of(p1.x + deltaX * i, p1.y + deltaY * i);
            const end = Point.of(p1.x + deltaX * (i + 1), p1.y + deltaY * (i + 1));
            segments.push(new Segment(start, end));
        }
        return segments;
    }    
    private constructor(p1: Point, p2: Point) {
        super();
        this.p1 = p1;
        this.p2 = p2;
    }

    
    svgPath(): string { 
        if (!this._svg) this._svg="L"+this.p2.svg();
        return this._svg;
    }

    hString(): string {
        if (!this._hString) this._hString = `${this.id()}[${this.p1.hString()},${this.p2.hString()}]`;
        return this._hString;
    }

    idPrefix():string { return "S" }
    
    start(): Point { return this.p1; }

    end(): Point { return this.p2; }

    _reverse = (): Segment => {
        return  Segment.ofNonsingular(this.p2, this.p1);
    }

    _bounds(): Bounds {
            let x = Math.min(this.p1.x, this.p2.x);
            let y = Math.min(this.p1.y, this.p2.y);
            let x2 = Math.max(this.p1.x, this.p2.x);
            let y2 = Math.max(this.p1.y, this.p2.y);
            return new Bounds(x, y, x2 - x, y2 - y);
    }

    split2(t: number): [Segment|singularity, Segment|singularity]{
        let x = this.p1.to(this.p2, t);
        return [Segment.of(this.p1, x), Segment.of(x, this.p2)];
    }

    pointAt(t:number):Point {
        return  this.p1.to(this.p2, t);
    }

    
    _linearize():Chain {
        let Chain = require('./Chain').Chain;
        return Chain._of([{path:this,t1:0,t2:1}]);
    }

    _bend(newStart: NPoint, newStartTangent: Angle, newEndTangent: Angle, newEnd: NPoint, stretch: DistortionOption = DISTORTION_STRETCHY): Path|singularity 
    {
        // xxx this was added
        if (newStart.equalsfp(newEnd)) {
            console.log("bending to a degenerate point");
            return Segment.of(newStart,newEnd);
        }
        
        let Cubic = require('./Cubic').Cubic;
        let cub: typeof Cubic | singularity = Cubic._of(this.p1, this.p1, this.p2, this.p2);
        if (cub === singularity) return cub;
        return cub.bend(newStart, newStartTangent, newEndTangent, newEnd, stretch);
    }

    length():number {
        return this.p1.dist(this.p2);
    }
    isErrant(tolerance: number): boolean {
        return false;
    }

    applyLinearTransform(f: (_: Point) => Point): Path|singularity {
        return Segment.of(f(this.p1), f(this.p2));
    }

    // split within precision of the point and break
    // into two pieces  
    // or else return [this]
    // xx
    splitForPoint3(p: Point, maxRadius: number): [Path|singularity, Path|singularity]|null {
        let x: Point = Point.forP(closestPoint(this.p1, this.p2, p));
        // if (x.dist(this.p1) < endpointPrecision || x.dist(this.p2) < endpointPrecision) {
        //     return [this];
        // }
        if (x.equals(this.p1)) return [singularity,this];
        if (x.equals(this.p2)) return [this,singularity];
        if (x.dist(p) < maxRadius) {
            return [Segment.of(this.p1, x), Segment.of(x, this.p2)];
        }
        return null;
    }

    _findPoint(p: Point, coarsePrecision: number, finePrecision: number, recursionLimit: number): SplitWithPrecision {
        let found = closestPoint(this.p1, this.p2, p);
        let dist = p.dist(found);
        if (dist <= coarsePrecision) return {precision: dist, t: found.t}
        return null;
    }

    _findPoint2(p:Point, maxRadius:number):SplitWithPrecision|null {
        let coarsePrecision=maxRadius;
        let found = closestPoint(this.p1, this.p2, p);
        let dist = p.dist(found);
        if (dist <= coarsePrecision) return {precision: dist, t: found.t}
        return null;
    }
    
    startTangent(): Angle {
        return new Angle(this.p2.minus(this.p1));
    }

    endTangent(): Angle {
        return new Angle(this.p2.minus(this.p1));
    }

    static distancePointToLineSegment(point:P,segmentStart:P,segmentEnd:P): number {
        const { x: px, y: py } = point;
        const { x: ax, y: ay } = segmentStart;
        const { x: bx, y: by } = segmentEnd;
        const dx = bx - ax;
        const dy = by - ay;
        
        // xxx this was added
        if (dx===0&&dy===0) { 
            // degenerate segment
            return Point.forP(point).dist(segmentStart);
        }
        if ((dx * dx + dy * dy)<0.0000001) {
            // console.log('too small')
        }
        
        
        const t = ((px - ax) * dx + (py - ay) * dy) / (dx * dx + dy * dy);
        if (t < 0) {
            // P is closest to A
            return Math.sqrt((px - ax) * (px - ax) + (py - ay) * (py - ay));
        } else if (t > 1) {
            // P is closest to B
            return Math.sqrt((px - bx) * (px - bx) + (py - by) * (py - by));
        } else {
            // nearest point on the segment
            const near = {x: ax + t * dx, y: ay + t * dy};
            return Math.sqrt(
                (px - near.x) * (px - near.x) +
                (py - near.y) * (py - near.y)
            );
        }
    }
    isSegment():boolean {
        return true;
    }

    isSingularity():singularity|false {
        return this.p1.equals(this.p2) ? singularity : false;
    }
}