import React, {useEffect} from "react";
import {StateContext} from "../App";
import './Inspect.css';
import './Toolbar.css';
import {
    DISTORTION_ORTHORGONAL,
    DISTORTION_STRETCHY,
    END_TANGENTS_FIXED,
    END_TANGENTS_FLOPPY, END_TANGENTS_FREE
} from "../../Math/DragOptions";

export const Inspect = () => {
    const {state, setState} = React.useContext(StateContext);
    // const { state, setState } = React.useContext(StateContext);

    const handleToolSelection = (tool: string) => {
        setState({...state, inspectToolSelection: tool});
    };

    let loose = state.mos.looseEndsReport().totalCount;
    let pointyCount = state.mos.pointyCornersReport().totalCount;
    let tangentCount = state.mos.tangentCornersReport().totalCount;
    let straightenIntersectionsCount = state.mos.straightenIntersectionsReport(state.straightenTolerance).totalCount;
    return <div>
        <table className="toolIconSet" width="100%"><tbody>
            <tr>
                <td width="33.3%" onClick={()=>handleToolSelection("Loose Ends")} className={state.inspectToolSelection==='Loose Ends'&&"toolIcon selected"||"toolIcon"}>
                    <div >
                        <>
                            {loose && <div className="badge">{loose}</div> }
                            <br/>Loose Ends
                        </>
                    </div>
                </td>
                <td width="33.3%"  onClick={()=>handleToolSelection("PointyCorners")}  className={state.inspectToolSelection==='PointyCorners'&&"toolIcon selected"||"toolIcon"}>
                    <div>
                        {pointyCount>0 && <div className="badge">{pointyCount}</div> }
                        <br/>Pointies
                    </div>
                </td>
                <td width="33.3%"  onClick={()=>handleToolSelection("TangentCorners")}  className={state.inspectToolSelection==='TangentCorners'&&"toolIcon selected"||"toolIcon"}>
                    <div>
                        {tangentCount>0 && <div className="badge">{tangentCount}</div> }
                        <br/>Tangents
                    </div>
                </td>
            </tr>
        <tr>
            <td width="33.3%"  onClick={()=>handleToolSelection("StraightenIntersections")}  className={state.inspectToolSelection==='StraightenIntersections'&&"toolIcon selected"||"toolIcon"}>
                <div>
                    {straightenIntersectionsCount>0 && <div className="badge">{straightenIntersectionsCount}</div> }
                    <br/>Straighten<br/>Intersections
                </div>
            </td>
        </tr>
        </tbody></table>
        <hr/>
        { state.inspectToolSelection==='StraightenIntersections'&& <StraightenIntersectionsOptions/>}
        { state.inspectToolSelection==='Loose Ends'&& <LooseEndsOptions/>}
        { state.inspectToolSelection==='PointyCorners'&& <PointyCornersOptions/>}
        { state.inspectToolSelection==='TangentCorners'&& <TangentCornersOptions/>}
    </div>;
};

const LooseEndsOptions: React.FC = () => {
    const {state, setState} = React.useContext(StateContext);
    let report = state.mos.looseEndsReport();
    function commit() {
        setState({...state, mos:report.autoFixed, undo:[state.mos, ...state.undo]});
    }
    return <>
        {
            !report.totalCount ? 
            <>There are no loose ends.</>
            : report.autoFix.size ? 
            <button onClick={commit}>Commit auto-fix</button>
            : 
            <>I couldn't fix the remaining <span className='badgeInline'>{report.totalCount}</span> loose ends, because I can't see where they're supposed to go. 
                You can fix them manually using the editing tools (or export to edit in your preferred vector editor).</>
        }
    </>;
};

const StraightenIntersectionsOptions: React.FC = () => {
    const {state, setState} = React.useContext(StateContext);
    let report = state.mos.straightenIntersectionsReport(state.straightenTolerance);
    const handleStraightenToleranceSlider = (event: React.ChangeEvent<HTMLInputElement>) => {
        setState({ ...state, straightenTolerance:parseFloat(event.target.value) });
    };
    function commit() {
        setState({...state, mos:report.autoFixed, undo:[state.mos, ...state.undo]});
    }
    return <>
        <table width="100%" border={0} cellPadding={0} cellSpacing={0}>
            <tbody>
            <tr >
                <td className="labelCell">
                    Tolerance:
                </td>
                <td  className="controlCell" ><input type="range" id="straightenToleranceSlider"
                                                     name="straightenToleranceSlider"
                                                     min={0} max={Math.PI/6} step={Math.PI/360}
                                                     value={state.straightenTolerance}
                                                     onChange={handleStraightenToleranceSlider}/>
                    = {Math.round(state.straightenTolerance*180/Math.PI*10)/10}
                </td>
            </tr>
            </tbody>
        </table>
        <hr/>
        {
            report.autoFix.size ? <div>
                <button onClick={commit}>Commit auto-fix</button>
            </div>: <>Nothing to fix</>
            
        }
    </>;
};

const PointyCornersOptions: React.FC = () => {
    const {state, setState} = React.useContext(StateContext);
    let report = state.mos.pointyCornersReport();
    return <>
        {
            !report.totalCount ? <>There are no pointy corners.</>
                               : <>You can fix them manually</>
        }
    </>;
};

const TangentCornersOptions: React.FC = () => {
    const {state, setState} = React.useContext(StateContext);
    let report = state.mos.tangentCornersReport();
    return <>
        {
            !report.totalCount ? <>There are no tangent corners.</>
                               : <>You can fix them manually</>
        }
    </>;
};
